import React from 'react';
import Product from './model/Product';
import ProductContainer from './ProductContainer';
import ProductService from './services/ProductService';
import ProductToken from './model/ProductToken';
import ProductTokenService from './services/ProductTokenService';
import LaunchLog from '../../common/model/LaunchLog';
import LaunchLogService from '../../common/services/LaunchLogService';
import AppcuesService from '../../common/services/AppcuesService';

export default {
  path: [
    '/products/:id',
    '/products/:id/contents',
    '/products/:id/pages/:pageId',
    '/products/:id/bundles/:bundleId',
    '/products/:id/bundles/:bundleId/pages/:pageId?'
  ],
  exact: true,
  services: [
    new ProductService(),
    new ProductTokenService(),
    new LaunchLogService(),
    new AppcuesService()
  ],
  stores: [
    {
      name: 'product',
      spec: Product,
      saveLocally: false,
      initialState: {
        authors: [{
          firstName: '',
          lastName: '',
          otherName: ''
        }
        ],
        audioBook: '',
        basePath: '',
        backLinkUrl: '',
        bookID: '',
        catalogUrl: '',
        coverThumbnailUrl: '',
        format: '',
        id: '',
        indexId: '',
        isSumatra: false,
        mathmlOn: true,
        mobileReady: false,
        model: '',
        platform: '',
        status: '',
        thumbnailUrl: '',
        title: '',
        version: '',
        subscription: {
          allowAccess: false,
          businessModelCode: '',
          endDate: 0,
          startDate: 0,
          type: ''
        },
        serversideProcessed: false,
        serversideEnabled: false,
        serverSideUuid: '',
        twoPageLayoutLeft: ''
      }
    },
    {
      name: 'productToken',
      spec: ProductToken,
      saveLocally: true,
      initialState: {
        id: '',
        name: '',
        value: '',
        expiryTime: 0,
        verified: false
      },
      init: (store) => {
        store.setVerified(false);
      }
    },
    {
        name: 'launchLog',
        spec: LaunchLog,
        saveLocally: true,
        initialState: {
          serverUpdatedDateTime: "",
          isImpersonated: false,
          deviceId: ""
        }
    }
  ],
  render: props => (
    <ProductContainer {...props} /> // eslint-disable-line react/prop-types
  )
};
