import { inject, observer } from 'mobx-react';
import { getSnapshot } from 'mobx-state-tree';
import PropTypes from 'prop-types';
import Framework, { shapes } from '@greenville/framework';
import { withLDConsumer } from 'launchdarkly-react-client-sdk';
import { AuthHomeHeader } from '@authhome/header';
import { Marquee } from '@authhome/marquee';
import { Library, LibraryExpand } from '@authhome/library';
import { Catalog } from '@study/components';
import Footer from './Footer';
import OffersContainer from './OffersContainer';
import OffersPlugin from '../offers';
import OffersUtils from '../../common/utils/OffersUtils';
import Bookshelf from '../bookshelf/model/Bookshelf';
import LastLocation from '../bookshelf/model/LastLocation';
import ProductToken from '../products/model/ProductToken';
import Offersm from '../offers/model/Offers';
import DisplayOrder from './model/DisplayOrder';
import env from '../../common/env';
import * as constants from '../../common/constants';
import ErrorCard from '../../common/components/ErrorCard';
import '../../assets/styles/glossaryPopup.css';
import CommonUtils from '../../common/utils/CommonUtils';
import PathUtils from '../../common/utils/PathUtils';
import AppcuesUtils from '../../common/utils/AppcuesUtils';
import UserDetails from '../userDetails/UserDetails';
import ChannelCarouselContainer from './ChannelCarouselContainer';
import MondlyLanguageContainer from './MondlyLanguageContainer';
import '../bookshelf/components/BookshelfStyles.scss';
import BackToHome from './BackToHome';
import LoadingCard from './Loader/LoadingCard';
import AccountLock from '../../common/components/AccountLock';
import AccountWarning from '../../common/components/AccountWarning';
import RemoveTitleDialog from '../../common/components/RemoveTitleDialog';
import '../../common/utils/chatbot.scss';

@inject('user', 'language', 'bookshelfStatus', 'bookshelf', 'userStatus', 'productToken', 'lastLocationStatus', 'offers', 'OffersStatus', 'displayorder')
@observer
class AuthHomeContainer extends Component {
  constructor(props) {
    super(props);
    this.OffersPlugin = new OffersPlugin(this);
    this.userDetail = {};
    this.isGtmInitiated = false;
    this.appcuesInitiated = false;
    this.hasMultiEntitlementLevel = false;
    this.state = {
      expandBookIndex: 0,
      dataFetched: false,
      isInitiated: false,
      isIgnored: false,
      threatLevel: 0,
      LDUserFlagsFetched: false,
      isHidden: false, //to be removed
      noOfDays: 0,
      isDismissed: false,//to be removed
      offers: [],
      offersError: false,
      showOfferDetails: false,
      showRedeem: false,
      selectedOfferDetails: {},
      showUserDetails: false,
      selectedIdBeforeDetailsPage: '',
      authHomeExpandContainer: '',
      isSearched: false,
      booksData: [],
      searchKeyWord: '',
      displayOrderFromApi: [],
      removeTitleOpen: false,
      removeBook: null,
      userFirstLogin: true,
      seeAllView: false
    };
    this.scrollTop = 0;
    this.scrollHeight = 0;
    document.getElementById('root').style.overflowX = 'hidden';
    this.chatBotOpen = PathUtils.getQueryParameterbyName('openChat');
    this.canadianProxy = PathUtils.getQueryParameterbyName('cp');
    document.body.classList.add("authHomePearsonPlus");
    this.accountMenuList;
    this.authHomeStackOrderData = {};
    this.marqueeProps = {};
    this.footerMenuList = CommonUtils.footerMenuList();
    this.marqueeDataFetched = false;
    this.marqueePropsUpdate = false;
    this.onLoadContainer = React.createRef();
  }


  componentDidMount() {
    const {
      bookshelf, offers, productToken, user, language
    } = this.props;
    const script = document.createElement('SCRIPT');
    script.src = env.BOOKSHELF_CONFIG.addon;
    script.crossOrigin = 'anonymous';
    script.async = true;
    document.head.appendChild(script);
    CommonUtils.initializeGTM();
    productToken.fetch(env.PRODUCT_TOKEN);
    Framework.getEventManager().on(constants.OFFERS_CONTENT_SUCCESS, () => {
      const { VIEW_PARTNER_EVENT_ACTION } = constants.OFFER_EVENT;
      const hasRedeem = OffersUtils.hasRedeem(getSnapshot(offers.eligiblity));
      this.setState({
        offers: getSnapshot(offers.offers_list),
        showRedeem: hasRedeem
      }, () => {
        if (this.state.showUserDetails) {
          this.setState({ showUserDetails: false });
          this.handleOffersClick(this.state.selectedIdBeforeDetailsPage, constants.OFFER_LOCATION.AUTHHOME);
        } else {
          this.setState({ selectedIdBeforeDetailsPage: '' });
        }
      });
      const additionalCustomField = this.OffersPlugin.generateCustomGAField();
      offers.offers_list.forEach(({ title: eventLabel, subtitle, design_id }) => {
        const customFields = {
          partner_id: eventLabel,
          partner_offer: subtitle,
          design_id,
          ...additionalCustomField
        };
        this.OffersPlugin.offersDispatchGAEvent(VIEW_PARTNER_EVENT_ACTION, eventLabel, customFields);
      });
    });
    Framework.getEventManager().on(constants.OFFERS_CONTENT_NOT_ELIGIBLE, () => {
      this.setState({ offers: [] });
    });

    this.OffersPlugin.init(constants.AUTH_HOME, () => {
      this.setState({
        offersError: true
      });
    });
    Framework.getEventManager().on(constants.BOOKSHELF_DATA_FETCHED, () => {
      CommonUtils.pushGlobalParams();
      // Push telemetry Global params and user telemetry events only once on page load when marin response is ready
      !this.state.isInitiated && !bookshelf.isError && this.pushTelemetry(user);
    });

    Framework.getEventManager().on(constants.USER_PROFILE_FETCHED, (user) => {
      Framework.getEventManager().on(constants.DISPLAY_ORDER_CONTENT_SUCCESS, (data) => {
        if (!CommonUtils.isEmpty(data)) {
          this.authHomeStackOrderData = data;
          const returnStackData = data;
          const displayOrderData = [];
          const stackProperty = ['marquee', 'library', 'channel', 'mondly', 'offer'];
          stackProperty.forEach((property) => {
            if (returnStackData[property]) {
              displayOrderData.push(returnStackData[property]);
            }
          });
          displayOrderData.sort((a, b) => a.displayOrder - b.displayOrder);
          displayOrderData.map((index, key) => {
            this.setState(prevState => ({
              displayOrderFromApi: [...prevState.displayOrderFromApi, index.id]
            }));
          });
        }
        this.setState({
          LDUserFlagsFetched: true
        });
      });
      bookshelf.fetch();
      this.accountMenuList = CommonUtils.getAccountMenuListItems(user);

      // const service=UserPreferencesService();
      if (user) {
        Framework.getEventManager().on(constants.USER_PREFERENCES_SUCESS, ((response) => {
          if (response) {
            this.setState({
              isDismissed: (response.swapTitleBanner ? response.swapTitleBanner.hasUserDismissed : false),
              userFirstLogin: (response.userActivity ? response.userActivity.isFirstLogin : true)
            });
            this.updateLaunchDarklyUser();
          }
          else {
            this.updateLaunchDarklyUser();
          }
        }));
        Framework.getEventManager().publish(constants.USER_PREFERENCES_REQUESTED);
      }

      Framework.getEventManager().on(constants.BOOKSHELF_DATA_FETCHED, () => {
        this.OffersPlugin.setUserRoleFetched(getSnapshot(bookshelf.books));
        if (this.chatBotOpen === 'true') {
          setTimeout(() => {
            const chatBotElement = document.getElementsByClassName('helpButtonEnabled');
            if (chatBotElement && chatBotElement.length > 0) {
              chatBotElement[0].click();
            }
          }, 3000);
        }
        var checkChatBotExist = setInterval(function () {
          const chatBotElement = document.getElementsByClassName('helpButtonEnabled');
          if (chatBotElement && chatBotElement.length > 0) {
            chatBotElement[0].setAttribute('aria-label', 'Expand chat');
            clearInterval(checkChatBotExist);
          }
        }, 100);
        !this.appcuesInitiated && this.initializeAppcues(user, bookshelf);
      });
    });
  }

  updateLaunchDarklyUser = (isIgnored = false) => {
    const { language, ldClient, user } = this.props;
    let displayeOrderPayload = {
      locale: language.currentLanguage
    }
    if (user.id && user.ldUserHash) {
      const ldUser = ldClient.getUser();
      ldClient.identify({
        key: user.id, options: { hash: user.ldUserHash }, custom: { isIgnored: isIgnored, hasPPlusSubscription: CommonUtils.getUserType(user) ? true : false }
      }, user.ldUserHash).then((res) => {
        console.log('ld success');
        // this.setState({
        //   LDUserFlagsFetched: true
        // });
        displayeOrderPayload.flag = res.ChannelExperimentDryrun;
        Framework.getEventManager().publish(constants.DISPLAY_ORDER_CONTENT_REQUESTED, displayeOrderPayload);
        this.initiateThreatLevel1(user, res['enable-account-abuse-level1'], res['enable-account-abuse-level2']);
        this.OffersPlugin.setLDFetched(res);
      }).catch((err) => {
        console.log('ld error');
        // this.setState({
        //   LDUserFlagsFetched: true
        // });
        displayeOrderPayload.flag = res.ChannelExperimentDryrun;
        Framework.getEventManager().publish(constants.DISPLAY_ORDER_CONTENT_REQUESTED, displayeOrderPayload);
        this.initiateThreatLevel1(user);
        this.OffersPlugin.LDFail(err);
      });
    }
  }

  initiateThreatLevel1 = (user, enableAccountAbuseLevel1 = false, enableAccountAbuseLevel2 = false) => {
    const deviceId = window.piSession && window.piSession.getDeviceId();
    const hasGpsSubscription = user ? user.gpsSubscriptions : null;
    if (hasGpsSubscription) {
      const isMojoUser = user.gpsSubscriptions.find(subscription => subscription.status === constants.ACTIVE ||
        subscription.status === constants.PAUSED);
      if (isMojoUser) {
        if (enableAccountAbuseLevel1 || enableAccountAbuseLevel2) {
          Framework.getEventManager().publish(constants.IES_DEVICE_LIST_REQUESTED, {
            type: 'threatStatus',
            data: { deviceId }
          });
        }
        Framework.getEventManager().on(constants.THREAT_STATUS_SUCCESS, (data) => {
          if (data.threatLevel === 1 && !data.hasUserWarningDisplayed && enableAccountAbuseLevel1) {
            this.setState({
              threatLevel: 1
            });
            Framework.getEventManager().publish(constants.IES_DEVICE_LIST_REQUESTED, {
              type: 'warningDisplayed'
            });
          } else if (data.threatLevel === 2 && enableAccountAbuseLevel2) {
            this.setState({
              threatLevel: 2
            });
            Framework.getEventManager().on(constants.ACCOUNT_LOCK_SUCCESS, () => {
              CommonUtils.dispatchGaEvent(
                constants.OTP_MODAL,
                constants.LOCK_ACCOUNT_EVENT_NAME,
                constants.LOCK_ACCOUNT_EVENT_ACTION,
                constants.LOCK_ACCOUNT_EVENT_LABEL
              );
            });
            Framework.getEventManager().publish(constants.IES_DEVICE_LIST_REQUESTED, {
              type: 'accountLock',
              data: {
                deviceId,
                userId: user.id
              }
            });
          }
        });
      }
    }
  }

  handleLogout = () => {
    const { user } = this.props;
    CommonUtils.dispatchGaEvent(constants.ACCOUNT_SETTINGS_CATEGORY, constants.ACCOUNT_SETTINGS, constants.ACCOUNT_SETTINGS_ACTION, constants.ACCOUNT_SETTINGS_SIGNOUT);
    const hasGpsSubscription = user ? user.gpsSubscriptions : null;
    if (hasGpsSubscription) {
      const isMojoUser = user.gpsSubscriptions.find(subscription => subscription.status === constants.ACTIVE ||
        subscription.status === constants.PAUSED);
      if (isMojoUser) {
        const XUserId = 'X-UserId';
        const XAuthorization = 'X-Authorization';
        const domain = '.pearson.com';
        document.cookie = `${XUserId}=;path=/;domain=${domain};expires=Thu, 18 Dec 2013 12:00:00 UTC;`;
        document.cookie = `${XAuthorization}=;path=/;domain=${domain};expires=Thu, 18 Dec 2013 12:00:00 UTC;`;

        const piSession = window.piSession;
        piSession.on(piSession.LogoutEvent, function () {
          const pmcBaseUrl = `${env.BOOKSHELF_CONFIG.pmcSearch}`;
          const logoutUrl = `${pmcBaseUrl}/store/en-us/pearsonplus/logout`;
          const homePageUrl = `${pmcBaseUrl}/en-us/pearsonplus.html`

          window.location.href = `${logoutUrl}?sessionTimedOut=false&consoleHomePageUrl=${homePageUrl}`;
        });
        piSession.logout();
      } else {
        Framework.logout();
      }
    } else {
      Framework.logout();
    }
  }

  handleConfirmRemoveTitle = () => {
    this.handleRemoveEntitlement(this.state.removeBook);
  }

  handleSortChange = (sortValue, selected) => {
    const eventLabel = (selected === 'A to Z' || selected === 'Z to A')
      ? `Alphabetical (${selected.replace(' to ', '>')})`
      : selected;
    const { SEARCH_EVENT } = constants;
    CommonUtils.dispatchGaEvent(SEARCH_EVENT.EVENT_CATEGORY, SEARCH_EVENT.EVENT_NAME, SEARCH_EVENT.EVENT_ACTION, eventLabel);
  }

  handleInfoTitleClick = (book, bookOrder) => {
    const { DASHBOARD_NAVIGATION_EVENT_CATEGORY, DASHBOARD_NAVIGATION_EVENT, BOOK_MENU_EVENT } = constants;
    const containerOrder = this.state.displayOrderFromApi.indexOf('library');
    const { book_id, product_id, title } = book;
    const customFields = {
      book_id,
      product_id: CommonUtils.toString(product_id),
      textbook_name: title,
      book_order: CommonUtils.toString(bookOrder),
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    CommonUtils.dispatchGaEvent(DASHBOARD_NAVIGATION_EVENT_CATEGORY, DASHBOARD_NAVIGATION_EVENT, BOOK_MENU_EVENT.EVENT_ACTION, BOOK_MENU_EVENT.INFO_EVENT_LABEL, customFields);
  }

  handleRemoveTitlePopup = (book, bookOrder) => {
    this.setState({ removeTitleOpen: true, removeBook: book });
    const { DASHBOARD_NAVIGATION_EVENT_CATEGORY, DASHBOARD_NAVIGATION_EVENT, BOOK_MENU_EVENT } = constants;
    const containerOrder = this.state.displayOrderFromApi.indexOf('library');
    const { book_id, product_id, title } = book;
    const customFields = {
      book_id,
      product_id: CommonUtils.toString(product_id),
      textbook_name: title,
      book_order: CommonUtils.toString(bookOrder),
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    CommonUtils.dispatchGaEvent(DASHBOARD_NAVIGATION_EVENT_CATEGORY, DASHBOARD_NAVIGATION_EVENT, BOOK_MENU_EVENT.EVENT_ACTION, BOOK_MENU_EVENT.REMOVE_EVENT_LABEL, customFields);
  }

  handleRemoveTitleClose = () => {
    this.setState({ removeTitleOpen: false, removeBook: null });
  }

  handleRemoveEntitlement = (bookId, isBanner = false) => {
    const payload = {
      data: {
        sourceContext: constants.MOJO,
        subscriptionNumber: bookId.product_entitlements.subscription_id,
        products: [
          {
            productId: bookId.product_id
          }
        ]
      },
      bookId: bookId.product_id,
      isBanner
    };
    Framework.getEventManager().publish(constants.REMOVE_ENTITLEMENT, payload);
    this.setState({
      dataFetched: false
    });
    this.marqueePropsUpdate = true;
    this.handleRemoveTitleClose();
  };

  handleCodeRedeemedSurvey = (customFields) => {
    const { user, bookshelf } = this.props;
    const shouldDisplaySurvey = customFields[constants.APPCUES_BOOKSTORE_SURVEY];
    const financialAid = CommonUtils.hasSubscriptions(user, constants.CHANNEL_PARTNER);
    const mojoBooks = CommonUtils.getProductByBMC(bookshelf, constants.DIRECT_TO_CONSUMER);
    const hasMojoBooks = mojoBooks.length > 0;
    let surveyCookies = CommonUtils.getCookie(constants.BOOKSTORE_SURVEY);
    if (surveyCookies) {
      surveyCookies = CommonUtils.deCodeBase64(surveyCookies);
    }
    const currentSessionId = window.piSession.getContextId();
    const userData = { userId: user && user.id };
    if (shouldDisplaySurvey) {
      if (!surveyCookies || (surveyCookies.sessionId !== currentSessionId)) {
        /* console log and alert will be removed after testing appcue event */
        console.log('SURVERY APPCUE TRIGGERED...');
        const eventData = {
          eventName: constants.APPCUES_BOOKSTORE_USER,
          eventProperties: {}
        };
        AppcuesUtils.handleEvent(constants.APPCUES_TRACK_EVENT, null, eventData);
        AppcuesUtils.handleEvent(constants.APPCUES_ADD_CUSTOM_FIELDS, userData, { [constants.APPCUES_BOOKSTORE_SURVEY]: false });
        CommonUtils.setCookie(constants.BOOKSTORE_SURVEY, '', -99999999);
      }
    }
    if (!shouldDisplaySurvey) {
      if (financialAid && !hasMojoBooks && !surveyCookies) {
        const cookieData = {
          sessionId: currentSessionId,
          [constants.BOOKSTORE_TITLE_ADDED]: false
        };
        AppcuesUtils.handleEvent(constants.APPCUES_ADD_CUSTOM_FIELDS, userData, { [constants.APPCUES_BOOKSTORE_SURVEY]: false });
        CommonUtils.setCookie(constants.BOOKSTORE_SURVEY, CommonUtils.enCodeBase64(cookieData));
      } else if (financialAid && hasMojoBooks && surveyCookies) {
        AppcuesUtils.handleEvent(constants.APPCUES_ADD_CUSTOM_FIELDS, userData, { [constants.APPCUES_BOOKSTORE_SURVEY]: true });
      }
    }
  };

  handleDotClick = (cardName, nextCardName, caurouselOrder) => {
    const containerOrder = this.state.displayOrderFromApi.indexOf('marquee');
    const customFields = {
      card_name: cardName,
      next_card_name: nextCardName,
      carousel_order: CommonUtils.toString(caurouselOrder),
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    CommonUtils.dispatchGaEvent(constants.AUTH_HOME_CATEGORY, constants.EVENT_AUTHHOMEEVENT, constants.MARQUEE_DOT_CLICK_ACTION, constants.DOTS, customFields);
  }

  /**
   * A method to handle arrow click
   *
   * @param {Object} bookData
   * @param {Object} bookOrder
  */
  // TODO: Sending GA event on book expanding
  handleExpandIconClick = (bookData, bookOrder, expand = true) => {
    const expandBookIndex = expand ? (bookOrder - 1) : null;
    this.setState({ expandBookIndex });
    const containerOrder = this.state.displayOrderFromApi.indexOf('library');
    const { book_id, product_id, title } = bookData;
    const customFields = {
      book_id,
      product_id: CommonUtils.toString(product_id),
      book_order: CommonUtils.toString(bookOrder),
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    if (expand === true) {
      const { EVENT_CATEGORY, EXPAND_ICON_EVENT_ACTION } = constants.LIBRARY_EVENT;
      CommonUtils.dispatchGaEvent(EVENT_CATEGORY, constants.EVENT_AUTHHOMEEVENT, EXPAND_ICON_EVENT_ACTION, bookData.title, customFields);
    } else {
      customFields.textbook_name = title;
      const { EVENT_CATEGORY, COLLAPS_ICON_EVENT_ACTION } = constants.LIBRARY_EVENT;
      CommonUtils.dispatchGaEvent(EVENT_CATEGORY, constants.EVENT_AUTHHOMEEVENT, COLLAPS_ICON_EVENT_ACTION, bookData.title, customFields);
    }
  }

  handleButtonClick = (buttonText, cardName, totalCards, caurouselOrder) => {
    const containerOrder = this.state.displayOrderFromApi.indexOf('marquee');
    const customFields = {
      total_number_of_cards: CommonUtils.toString(totalCards),
      card_name: cardName,
      carousel_order: CommonUtils.toString(caurouselOrder),
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    CommonUtils.dispatchGaEvent(constants.AUTH_HOME_CATEGORY, constants.EVENT_AUTHHOMEEVENT, constants.MARQUEE_BUTTON_CLICK_ACTION, buttonText, customFields);
  }

  isReadyToRender = () => {
    const user = Framework.getStoreRegistry().getStore('user');
    let books = Framework.getStoreRegistry().getStore('bookshelf');
    const userLanguage=this.props.language;
    books = getSnapshot(books);
    this.userDetail = user;
    let gpsSubscription = {};
    if (user && user.gpsSubscriptions) {
      this.hasMultiEntitlementLevel = CommonUtils.hasEntitlementLevel(user, constants.ENTITLEMENTLEVEL_MULTI);
      gpsSubscription = user.gpsSubscriptions.find(subscription => subscription.status === constants.ACTIVE ||
        subscription.status === constants.PAUSED);
      this.userDetail.pearsonSubscriptionId = gpsSubscription && gpsSubscription.subscriptionId;
      const offerLoadDetails = user.gpsSubscriptions.find(subscription => (
        (subscription.status === constants.ACTIVE || subscription.status === constants.PAUSED)
        && subscription.entitlementTier && subscription.entitlementTier === constants.STANDARD
      ));
      if (offerLoadDetails && this.state.isInitiated && !this.isGtmInitiated) {
        CommonUtils.onloadOfferEventPush();
        this.isGtmInitiated = true;
      }
    }
    if (this.state.dataFetched && this.state.LDUserFlagsFetched && !this.marqueeDataFetched) {
      if (this.state.userFirstLogin && ((books.books).length > 0)) {
        const eventData = {
          eventType: 'PUT',
          payload: {
            userActivity: {
              isFirstLogin: false
            }
          }
        }
        Framework.getEventManager().publish(constants.USER_PREFERENCES_REQUESTED, eventData);
      }
      CommonUtils.initChatbotScript(user);
      this.marqueeDataFetched = true;
      let swapTitleData;
      const noOfDays = CommonUtils.getNoOfDays(gpsSubscription?.endDate);
      if (CommonUtils.getUserType(user) && user.gpsSubscriptions && !gpsSubscription.swapTitleCount && noOfDays <= 21) {
        swapTitleData = {
          noOfDays,
          bookName: books && books.books[0].book_title
        };
      }
      const defaultMarqueeProps = {
        isPlaying: true,
        durationInterval: 10000,
        userName: user.firstName,
        pearsonSubscriptionId: gpsSubscription && gpsSubscription.subscriptionId,
        MarqueeData: CommonUtils.getMarqueeData(this.authHomeStackOrderData, user, books, this.state.userFirstLogin, swapTitleData,userLanguage),
        onDotClick: this.handleDotClick,
        onButtonClick: this.handleButtonClick
      };
      this.marqueeProps = { ...defaultMarqueeProps, ...swapTitleData };
    }

    if (this.state.dataFetched && this.marqueePropsUpdate) {
      this.marqueePropsUpdate = false;
      const hasPearsonPlusBooks = CommonUtils.getProductByBMC(books, constants.DIRECT_TO_CONSUMER).length > 0;
      if (!hasPearsonPlusBooks) {
        let swapTitleData;
        const noOfDays = CommonUtils.getNoOfDays(gpsSubscription?.endDate);
        if (CommonUtils.getUserType(user) && user.gpsSubscriptions && !gpsSubscription.swapTitleCount && noOfDays <= 21) {
          swapTitleData = {
            noOfDays,
            bookName: books && books.books[0].book_title
          };
        }
        this.marqueeProps.MarqueeData = CommonUtils.getMarqueeData(this.authHomeStackOrderData, user, books, this.state.userFirstLogin, swapTitleData,userLanguage);
      }
    }

    return (this.state.dataFetched && user.gpsSubscriptions && this.state.LDUserFlagsFetched);
  }

  /**
   * Method to initialize Appcues
   */
  initializeAppcues = (user, bookshelf) => {
    this.appcuesInitiated = true;
    let isInstructor = false;
    if (bookshelf && bookshelf.books && bookshelf.books.length > 0) {
      bookshelf.books.some((title) => {
        if ((title.product_entitlements.user_role === constants.PDF_CONSTANTS.USER_ROLE.INSTRUCTOR)
          || (title.product_entitlements.user_role === constants.PDF_CONSTANTS.USER_ROLE.EDUCATOR)) {
          isInstructor = true;
        }
        if (title.courses.length > 0) {
          title.courses.some((course) => {
            if ((course.user_role === constants.PDF_CONSTANTS.USER_ROLE.INSTRUCTOR) || (course.user_role === constants.PDF_CONSTANTS.USER_ROLE.EDUCATOR)) {
              isInstructor = true;
            }
          });
        }
      });
    }
    Framework.getEventManager().on(constants.APPCUES_USER_IDENTIFIED, () => {
      CommonUtils.trackAppcuesHomeEvent(
        user, bookshelf, isInstructor
      );
      AppcuesUtils.getCustomFields(user).then(this.handleCodeRedeemedSurvey);
    });
    // check if user is identified
    this.identifyUser(user);
  }

  /**
   * Method to identify user for Appcues
   *
   * @param {Object} user - user information
   */
  identifyUser(user) {
    const userData = {
      userId: user && user.id
    };

    if (userData.userId) {
      AppcuesUtils.handleEvent(constants.APPCUES_IDENTIFY_USER, userData);
    }
  }

  /**
   * A method to handle book click
   *
   * @param {Object} bookData
   * @param {Object} bookOrder
  */
  // TODO: Revisit and change the redirection logic based on BMC once data is available
  handleBookClick = (bookData, type, courseId = '', bookOrder) => {
    const { match } = this.props;
    const lastLocationStore = Framework.getStoreRegistry().getStore('lastlocation');
    let urlPrefix = match.url;
    // TODO: Use this value for redirection when paper api is ready
    const {
      book_id,
      product_id,
      product_entitlements,
      title
    } = bookData;
    const { SOCIAL } = constants;
    const {
      username, smsUserId, id
    } = Framework.getStoreRegistry().getStore('user');
    const containerOrder = this.state.displayOrderFromApi.indexOf('library');
    const customFields = {
      book_id,
      product_id: CommonUtils.toString(product_id),
      book_order: CommonUtils.toString(bookOrder),
      location: this.state.seeAllView ? 'See all view' : 'Auth home',
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    if (type === SOCIAL) {
      const { SOCIAL_EVENT, CLICK_EVENT_NAME } = constants;
      CommonUtils.dispatchGaEvent(SOCIAL, SOCIAL_EVENT.EVENT_NAME, CLICK_EVENT_NAME, SOCIAL_EVENT.EVENT_LABEL, customFields);
    }
    const { DASHBOARD_NAVIGATION_EVENT_CATEGORY, BOOK_EVENT } = constants;
    CommonUtils.dispatchGaEvent(DASHBOARD_NAVIGATION_EVENT_CATEGORY, BOOK_EVENT.EVENT_NAME, BOOK_EVENT.EVENT_ACTION, title, customFields);
    const userLastLocation = lastLocationStore.userLocation && lastLocationStore.userLocation.find(location => location.bookId === product_id);
    const getContextPath = courseId && courseId !== '' ? `&context_id=${courseId}` : '';
    const userLocationDetails = userLastLocation ? `&location=${encodeURIComponent(JSON.stringify(userLastLocation))}` : '';
    const userPreferredType = `&userPreferredType=${type}`;
    const canadianProxy = this.canadianProxy === '1' ? '&cp=Y' : '';
    let productId = product_id;
    let isIntegratedLaunch = false;
    const integratedBMC = [constants.INTEGRATED_SMS, constants.INTEGRATED_RUMBA, constants.CG_RENEWALS];
    const businessModelList = [constants.DIRECT_TO_LEARNER, constants.DIRECT_TO_LEARNER_RENEWAL, constants.COURSE_WARE, constants.DIRECT_TO_CONSUMER];
    const businessModelCode = product_entitlements.business_model_code;

    if (integratedBMC.includes(businessModelCode)) {
      productId = book_id;
      isIntegratedLaunch = true;
    }

    if (urlPrefix.indexOf('/home') > -1) {
      urlPrefix = match.url.split('/home')[0];
    }
    if (isIntegratedLaunch) {
      window.location.href = `${env.PAPER_API_BASE_URL}?custom_bookid=${productId}&custom_smsid=${smsUserId}&custom_pi_id=${id}${canadianProxy}${getContextPath}${userLocationDetails}${userPreferredType}&custom_person_id=${username}`;
    } else if (businessModelList.includes(businessModelCode) && !window.location.host.includes(constants.ETEXT_PERF)) {
      if (courseId) {
        window.location.href = `${urlPrefix}/courses/${courseId}?${userLocationDetails}${userPreferredType}`;
      } else {
        window.location.href = `${urlPrefix}/products/${productId}?${userLocationDetails}${userPreferredType}`;
      }
    } else if (courseId) {
      window.location.href = `${urlPrefix}/courses/${courseId}?${userLocationDetails}${userPreferredType}`;
    } else {
      window.location.href = `${urlPrefix}/products/${productId}?${userLocationDetails}${userPreferredType}`;
    }
  }


  pushTelemetry = (user) => {
    const loggedUser = user;
    CommonUtils.logUserTelemetryEvent('', loggedUser.id);
    CommonUtils.sendPageViewEvent(loggedUser.gpsSubscriptions);
    CommonUtils.initTelemetryPageAction();
    const { books } = Framework.getStoreRegistry().getStore('bookshelf');
    const { activeBooksLength, inactiveBooksLength } = CommonUtils.getBooksLength(books);
    const noOfContainers = document.querySelectorAll(constants.TRACKING_CONTAINERS);
    const customFields = { num_active_titles: activeBooksLength, num_inactive_titles: inactiveBooksLength, num_containers: noOfContainers.length };
    CommonUtils.dispatchGaEvent(constants.AUTHHOME_APPLICATION, constants.AUTHHOME_APPLICATION_EVENT_NAME, constants.AUTHHOME_APPLICATION_EVENT_ACTION, '', customFields);
    this.setState({ isInitiated: true });
  }

  /**
   * Function to save dismiss swap title notification
   */
  handleDismiss = () => {
    this.setState({
      isDismissed: true
    })
    const eventData = {
      eventType: 'PUT',
      payload: {
        swapTitleBanner: {
          hasUserDismissed: true
        }
      }
    }
    Framework.getEventManager().publish(constants.USER_PREFERENCES_REQUESTED, eventData);
  }

  /**
   * Handles Offer Click
   *
   * @param {*} id
   */
  handleOffersClick = (id, location) => {
    const { offers } = this.props;
    const { showOfferDetails } = this.state;
    const {
      LOCATION: {
        OFFER_DETAILS
      }
    } = constants.OFFER_EVENT;
    const containerOrder = this.state.displayOrderFromApi.indexOf('offer');
    const customFields = {
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    this.OffersPlugin.offerActionGAEvent(id, location, customFields);
    if (offers.showUserDetails && location !== OFFER_DETAILS) {
      this.setState({
        showUserDetails: true,
        selectedIdBeforeDetailsPage: id
      });
    } else {
      const moreAvailableOffers = OffersUtils.getMoreAvailableOffers(offers.offers_list, id);
      const selectedOfferDetails = OffersUtils.getSelectedOfferDetails(offers.offers_list, id);
      if (!showOfferDetails) {
        this.scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        this.scrollHeight = document.body.scrollHeight;
      }

      this.setState({
        showOfferDetails: true,
        selectedOfferDetails,
        offers: moreAvailableOffers,
        authHomeExpandContainer: 'showOfferPage'
      });
      const scrollOptions = { top: 0 };
      if (location === OFFER_DETAILS) {
        scrollOptions.behavior = 'smooth';
      }
      window.scrollTo(scrollOptions);
    }
  }

  onBackClick = (event) => {
    if (this.state.showOfferDetails && this.state.selectedOfferDetails && this.state.selectedOfferDetails.id) {
      this.handleOfferScreenChange(event, this.state.selectedOfferDetails.id);
    }

    this.setState({
      authHomeExpandContainer: '',
      showOfferDetails: false,
      seeAllView: false,
      isSearched: false
    });
  }
  OnSeeAllChannels = () => {
    this.setState({
      authHomeExpandContainer: 'ChannelsExpanded',
      seeAllView: true
    })
  }
  onSeeAll = () => {
    this.setState({
      authHomeExpandContainer: 'Library',
      seeAllView: true
    }, () => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    });
    // sending GA events for Library
    const containerOrder = this.state.displayOrderFromApi.indexOf('library');
    const customFields = {
      event_value: CommonUtils.toString((containerOrder + 1))
    };
    const { EVENT_CATEGORY, EVENT_LABLE_SEE_ALL, EVENT_ACTION_SEE_ALL } = constants.LIBRARY_EVENT;
    CommonUtils.dispatchGaEvent(EVENT_CATEGORY, constants.EVENT_AUTHHOMEEVENT, EVENT_ACTION_SEE_ALL, EVENT_LABLE_SEE_ALL, customFields);
  }

  hasShowChannelsCarousel = () => {
    const {
      language,
      flags
    } = this.props;
    const isStudent = CommonUtils.getUserRole();
    const isEnglishLang = language.currentLanguage.slice(0, 2).toLowerCase() === 'en';
    const LD_CHECK = ((isStudent && flags.enableStudyChannelCarousel) || (!isStudent && flags.enableStudyChannelEducator));
    return LD_CHECK && isEnglishLang
  };

  hasShowMondly = () => {
    const {
      flags
    } = this.props;
    return flags.enableMondlyLangComponent;
  };
  handleFilterBooks = (books, searchtext) => {
    const hasSearchAvailable = searchtext && searchtext.length > 0;
    const seeAll = this.state.seeAllView;
    let showOfferPage = true;
    if (!hasSearchAvailable && this.state.authHomeExpandContainer === 'Library') {
      showOfferPage = false;
      this.handleOfferScreenChange();
    }
    this.setState({
      authHomeExpandContainer: (hasSearchAvailable || seeAll) ? 'Library' : '',
      isSearched: !!hasSearchAvailable,
      booksData: hasSearchAvailable ? books : [],
      searchKeyWord: searchtext,
      showOfferDetails: showOfferPage
    });
  }

  handleAccountAbuseClose = () => {
    this.setState({
      threatLevel: 0
    });
  }

  handleAccountLockClose = () => {
    CommonUtils.dispatchGaEvent(constants.TL2_CATEGORY, constants.TL2_EVENT_NAME, constants.TL2_EVENT_ACTION, constants.TL2_EVENT_LABEL);
    Framework.logout();
  }

  handleSearchEvent = (eventCategory, event, action, label, customFields = null, redirectURL = null) => {
    CommonUtils.dispatchGaEvent(eventCategory, event, action, label, customFields);
    if (redirectURL !== null) {
      window.open(redirectURL, '_self');
    }
  }

  renderBasedOnOrder = () => {
    const {
      bookshelf,
      language,
      flags,
      user
    } = this.props;
    let renderData = [];
    const intermediateData = [];
    let mainContainer = [];

    if (this.state.displayOrderFromApi.length > 0) {
      this.state.displayOrderFromApi.map((index, key) => {
        if (index === 'marquee') {
          renderData.push(
            <Fragment><div className={`slide-container`} data-title={'Marquee'}><Marquee {...this.marqueeProps} /></div></Fragment>
          )
        }
        if (index === 'library' && bookshelf.books.length > 0) {
          intermediateData.push(
            <Fragment>
              <div className={`library-container`} id="library-authhome" data-title={'Library'}>
                <Library
                  locale={language.currentLanguage}
                  books={getSnapshot(bookshelf.books)}
                  config={
                    {
                      enableSocial: flags.enableSocial,
                      removeEntitlement: flags.removeEntititlement && this.hasMultiEntitlementLevel
                    }
                  }
                  expandBookIndex={this.state.expandBookIndex}
                  onSeeAll={this.onSeeAll}
                  onBookClick={this.handleBookClick}
                  onHandleExpand={this.handleExpandIconClick}
                  onInfoTitle={this.handleInfoTitleClick}
                  onRemoveTitle={this.handleRemoveTitlePopup}
                  userStatus={CommonUtils.getUserType(user)}
                  columns={{ xs: 3, sm: 3, md: 'auto', mlg: 'auto', lg: 'auto', xl: 'auto', xxl: 'auto' }}
                />
              </div></Fragment>
          )
        }
        if (index === 'channel' && this.hasShowChannelsCarousel()) {
          intermediateData.push(
            <Fragment>
              <div className='Channel-container' data-title={'Channels'}>
                <ChannelCarouselContainer
                  containerOrder={(key+1)}
                  userDetail={this.userDetail}
                  userType={this.OffersPlugin.isStudent ? 'general' : constants.EDUCATOR}
                  env={env.ENVIRONMENT}
                  locale={language.currentLanguage}
                  numberOfRows={flags.numberOfRowsChannels}
                  onSeeAllClick={this.OnSeeAllChannels}
                />
              </div>
            </Fragment>
          )
        }
        if (index === 'mondly' && this.hasShowMondly()) {
          intermediateData.push(
            <Fragment>
              <div className='mondly-container' data-title={'Mondly'}>
                <MondlyLanguageContainer
                  containerOrder={(key + 1)}
                  userDetail={this.userDetail}
                  env={env.ENVIRONMENT}
                  locale={language.currentLanguage}
                />
              </div>
            </Fragment>
          )
        }
        if (index === 'offer' && this.state.offers.length > 0) {
          intermediateData.push(
            <div className={`offers-container`} data-title={'Offers'}>
              <OffersContainer
                offers={this.state.offers}
                handleOfferClick={this.handleOffersClick}
                offersSubtitle={this.OffersPlugin.isStudent ? language.getMessage('offers.SUBTITLE_STUDENT') : language.getMessage('offers.SUBTITLE_INSTRUCTOR')}
                showOfferDetails={this.state.showOfferDetails}
                showRedeem={this.state.showRedeem}
                selectedOfferDetails={this.state.selectedOfferDetails}
                handleOfferScreenChange={this.handleOfferScreenChange}
                handleRedeemClick={this.OffersPlugin.handleRedeemClick}
                intl={language}
              />
            </div>
          )
        }
      });
    } else {
      renderData.push(
        <div>
          <Marquee {...this.marqueeProps} />
          <div className='maincontainer'>
            {bookshelf.books.length > 0 &&
              <div className='library-container' id="library-authhome" data-title={'Library'}>
                <Library
                  locale={language.currentLanguage}
                  books={getSnapshot(bookshelf.books)}
                  config={
                    {
                      enableSocial: flags.enableSocial,
                      removeEntitlement: flags.removeEntititlement && this.hasMultiEntitlementLevel
                    }
                  }
                  columns={{ xs: 3, sm: 3, md: 'auto', mlg: 'auto', lg: 'auto', xl: 'auto', xxl: 'auto' }}
                  expandBookIndex={this.state.expandBookIndex}
                  onSeeAll={this.onSeeAll}
                  onBookClick={this.handleBookClick}
                  onInfoTitle={this.handleInfoTitleClick}
                  onHandleExpand={this.handleExpandIconClick}
                  onRemoveTitle={this.handleRemoveTitlePopup}
                  userStatus={CommonUtils.getUserType(user)}
                />
              </div>}
            {this.hasShowChannelsCarousel() &&
              <div className='Channel-container' data-title={'Channels'}>
                <ChannelCarouselContainer
                  containerOrder={3}
                  userDetail={this.userDetail}
                  userType={this.OffersPlugin.isStudent ? 'general' : constants.EDUCATOR}
                  env={env.ENVIRONMENT}
                  locale={language.currentLanguage}
                  numberOfRows={flags.numberOfRowsChannels}
                  onSeeAllClick={this.OnSeeAllChannels}
                />
              </div>
            }
            {this.hasShowMondly() &&
              <div className='mondly-container' data-title={'Mondly'}>
                <MondlyLanguageContainer
                  containerOrder={(key + 1)}
                  userDetail={this.userDetail}
                  env={env.ENVIRONMENT}
                  locale={language.currentLanguage}
                />
              </div>
            }
            {
              this.state.offers.length > 0 && <div className='offers-container' data-title={'Offers'}>
                <OffersContainer
                  offers={this.state.offers}
                  handleOfferClick={this.handleOffersClick}
                  offersSubtitle={this.OffersPlugin.isStudent ? language.getMessage('offers.SUBTITLE_STUDENT') : language.getMessage('offers.SUBTITLE_INSTRUCTOR')}
                  showOfferDetails={this.state.showOfferDetails}
                  showRedeem={this.state.showRedeem}
                  selectedOfferDetails={this.state.selectedOfferDetails}
                  handleOfferScreenChange={this.handleOfferScreenChange}
                  handleRedeemClick={this.OffersPlugin.handleRedeemClick}
                  intl={language}
                />
              </div>
            }

          </div>
        </div>
      )
    }

    if (intermediateData.length > 0) {
      mainContainer.push(<div className='maincontainer'>{intermediateData}</div>);
    }
    renderData = [...renderData, ...mainContainer];
    return renderData;
  }

  handleMenuClick = (e, eventLabel) => {
    CommonUtils.dispatchGaEvent(constants.ACCOUNT_SETTINGS_CATEGORY, constants.ACCOUNT_SETTINGS, constants.ACCOUNT_SETTINGS_ACTION, eventLabel);
  }

  handleInitLoad = (container) => {
    if (container) {
      CommonUtils.pageContainerTracker();
      this.onLoadContainer = null;
    }
  }
  componentDidUpdate = () => {
    const isUserPending = this.props.productToken.verified ? this.props.userStatus.isPending : true;
    const isBookshelfPending = this.props.productToken.verified ? this.props.bookshelfStatus.isPending : true;
    const books = getSnapshot(this.props.bookshelf.books);
    if (!isUserPending && !isBookshelfPending || books) {
      this.onLoadContainer = 'Loaded';
      this.handleInitLoad(this.onLoadContainer)
    }
  }
  /**
   * Handles the back to home link and
   * search change when offer detail section
   * is visible
   */
  handleOfferScreenChange = (event = null, offerId = null) => {
    const { offers } = this.props;
    const { HOME_CLICK_EVENT_ACTION } = constants.OFFER_EVENT;
    this.setState({
      showOfferDetails: false,
      selectedOfferDetails: {},
      offers: offers.offers_list
    }, () => {
      let scrollHeightCheckCount = 0;
      this.checkScrollHeight = setInterval(() => {
        if (this.scrollHeight <= document.body.scrollHeight || scrollHeightCheckCount === 10) {
          window.scrollTo({ top: this.scrollTop });
          this.scrollTop = 0;
          clearInterval(this.checkScrollHeight);
        }

        scrollHeightCheckCount += 1;
      }, 100);
    });
    if (event && offerId) {
      const { title: partnerId } = OffersUtils.getSelectedOffer(getSnapshot(offers.offers_list), offerId);
      const customFields = { partner_id: partnerId };

      this.OffersPlugin.offersDispatchGAEvent(
        HOME_CLICK_EVENT_ACTION,
        event,
        customFields
      );
    }
  }
  /**
   * handle User detail pop up close
  */
  handleUserDetailClose = () => {
    this.setState({ showUserDetails: false });
  }

  /**
   * handle contact support click
   */
  handleContactSupport = () => {
    const { user } = this.props;
    const isPPlusUser = CommonUtils.getUserType(user);
    const supportLink = isPPlusUser
      ? constants.PPLUS_CUSTOMER_SUPPORT
      : constants.NON_PPLUS_CUSTOMER_SUPPORT;

    window.open(supportLink, '_blank');
  }
  render() {
    const {
      language,
      userStatus,
      bookshelf,
      user,
      flags
    } = this.props;
    const userId = user.id;
    const token = user.token; 
    const errorCode = userStatus.error && constants.ERROR_CODE_MARIN;
    Framework.getEventManager().on(constants.BOOKSHELF_DATA_FETCHED, () => {
      this.setState({
        dataFetched: true
      });
    });

    this.searchProps = {
      placeHodler: CommonUtils.getUserType(user) ? language.getMessage('search.searchPlaceHolderPPlus') : language.getMessage('search.searchPlaceHolderEText'), //'Search by ISBN, Title, or Author' : 'Search your list',
      language: language.currentLanguage,
      algoliaConfig: CommonUtils.algoliaConfig(),
      userType: CommonUtils.getUserType(user),
      books: getSnapshot(bookshelf.books),
      onhandleFilterBooks: this.handleFilterBooks,
      dispatchGaEvent: this.handleSearchEvent
    };


    return (
      (userStatus.error)
        ? <ErrorCard code={errorCode} />
        : (
          this.isReadyToRender() ?
            <div style={{ 'background': '#05112A' }}>
              <Fragment>
                <AuthHomeHeader
                  locale={language.currentLanguage}
                  logoRedirectUrl={CommonUtils.getUserType(this.userDetail) ? env.PLUS_URL : env.BOOKSHELF_CONFIG.logo}
                  isLoading={false}
                  onLogout={this.handleLogout}
                  onMenuListClick={this.handleMenuClick}
                  settingMenuList={this.accountMenuList}
                  user={this.userDetail}
                  {...this.searchProps}
                />
                {this.state.authHomeExpandContainer === '' &&
                  <div>
                    {this.renderBasedOnOrder()}
                  </div>
                }
                {
                  this.state.authHomeExpandContainer !== '' &&
                  <div className='expandedContainer'>
                    <BackToHome onBackClick={this.onBackClick} />
                    <div className="divider" />
                    {
                      this.state.authHomeExpandContainer === 'Library' &&
                      <div id="library-authhome" data-title={'Library'}>
                        <LibraryExpand
                          locale={language.currentLanguage}
                          books={this.state.isSearched ? this.state.booksData : getSnapshot(bookshelf.books)}
                          className="libraryExpand"
                          config={
                            {
                              enableSocial: flags.enableSocial,
                              removeEntitlement: flags.removeEntititlement && this.hasMultiEntitlementLevel
                            }
                          }
                          onBookClick={this.handleBookClick}
                          onSortChange={this.handleSortChange}
                          onInfoTitle={this.handleInfoTitleClick}
                          onHandleExpand={this.handleExpandIconClick}
                          onRemoveTitle={this.handleRemoveTitlePopup}
                          userStatus={CommonUtils.getUserType(user)}
                          searchKeyWord={this.state.searchKeyWord}
                        />
                      </div>
                    }
                    {
                      this.state.authHomeExpandContainer === 'ChannelsExpanded' &&
                      <Fragment>
                        <div className='Channel-container' data-title={'Channels'}>
                          <Catalog
                            getUserCallback={() => ({ userId, token })}
                            env={env.ENVIRONMENT}
                            locale={language.currentLanguage}
                          />
                        </div>
                      </Fragment>
                    }
                    {
                      this.state.authHomeExpandContainer === 'showOfferPage' &&
                      <div className={`offers-container`} data-title={'Offers'}>
                        <OffersContainer
                          offers={this.state.offers}
                          handleOfferClick={this.handleOffersClick}
                          offersSubtitle={this.OffersPlugin.isStudent ? language.getMessage('offers.SUBTITLE_STUDENT') : language.getMessage('offers.SUBTITLE_INSTRUCTOR')}
                          showOfferDetails={this.state.showOfferDetails}
                          showRedeem={this.state.showRedeem}
                          selectedOfferDetails={this.state.selectedOfferDetails}
                          handleOfferScreenChange={this.handleOfferScreenChange}
                          handleRedeemClick={this.OffersPlugin.handleRedeemClick}
                          intl={language}
                        />
                      </div>
                    }


                  </div>
                }

                <Footer {...this.footerMenuList} />
                {/* Modal popup dialog area*/}
                <RemoveTitleDialog
                  open={this.state.removeTitleOpen}
                  onClose={this.handleRemoveTitleClose}
                  onRemoveTitle={this.handleConfirmRemoveTitle}
                />
                {this.state.showUserDetails
                  && (
                    <UserDetails
                      handleContactSupport={this.handleContactSupport}
                      handleUserDetailClose={this.handleUserDetailClose}
                      isInstructor={!this.OffersPlugin.isStudent}
                    />
                  )}
                {this.state.threatLevel === 1 ? <AccountWarning open={true} onClose={this.handleAccountAbuseClose} /> : <></>}
                {this.state.threatLevel === 2 ? <AccountLock open={true} onClose={this.handleAccountLockClose} /> : <></>}
              </Fragment>
            </div>
            : <LoadingCard />
        )
    );
  }
}

AuthHomeContainer.propTypes = {
  bookshelf: shapes.modelOf(Bookshelf).isRequired,
  lastlocation: shapes.modelOf(LastLocation).isRequired,
  bookshelfStatus: shapes.state.isRequired,
  lastLocationStatus: shapes.state.isRequired,
  language: shapes.language.isRequired,
  ldClient: PropTypes.object.isRequired,
  match: shapes.match.isRequired,
  user: shapes.user.isRequired,
  userStatus: shapes.state.isRequired,
  productToken: shapes.modelOf(ProductToken).isRequired,
  offers: shapes.modelOf(Offersm).isRequired,
  displayorder: shapes.modelOf(DisplayOrder).isRequired,
  OffersStatus: shapes.state.isRequired,
  flags: PropTypes.object.isRequired
};

export default withLDConsumer()(AuthHomeContainer);