import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';

const style = () => ({
  headingContainer: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  quickCheckSubTitle: {
    color: '#FFFFFF',
    fontFamily: 'TT Commons',
    fontStyle: 'normal',
    fontWeight: 500,
    fontSize: 22,
    lineHeight: '24px',
    paddingBottom: 17,
    textAlign: 'center',
    paddingLeft: 20,
    paddingRight: 20
  },
  quickCheckTitle: {
    color: '#FFFFFF',
    fontFamily: 'TT Commons',
    fontStyle: 'normal',
    fontWeight: 700,
    fontSize: 32,
    lineHeight: '32px',
    paddingBottom: 8
  }
});

const StyleHeader = ({
  classes,
  headingText,
  subHeadingText
}) => (
  <div className={classes.headingContainer}>
    <span>
      <Typography classes={{ root: classes.quickCheckTitle }}>
        {headingText}
      </Typography>
    </span>
    <span>
      <Typography classes={{ root: classes.quickCheckSubTitle }}>
        {subHeadingText}
      </Typography>
    </span>
  </div>
);

StyleHeader.propTypes = {
  classes: PropTypes.object.isRequired,
  headingText: PropTypes.string,
  subHeadingText: PropTypes.string
};

StyleHeader.defaultProps = {
  headingText: '',
  subHeadingText: ''
};

export default withStyles(style)(StyleHeader);
