/* eslint-disable */
function updateChatBotSettings(settings) {
  const targetElement = document.querySelector('body \x3e main');
  settings.targetElement = targetElement || document.body;
}

const initESW = (gslbBaseURL, config) => {
  window.embedded_svc.settings.displayHelpButton = true; // Or false
  window.embedded_svc.settings.language = ''; // For example, enter 'en' or 'en-US'
  updateChatBotSettings(window.embedded_svc.settings);

  // window.embedded_svc.settings.defaultMinimizedText = 'Chat'; // (Defaults to Chat with an Expert)
  // window.embedded_svc.settings.disabledMinimizedText = 'Agent Live'; // (Defaults to Agent Offline)

  // window.embedded_svc.settings.loadingText = ''; // (Defaults to Loading)
  // window.embedded_svc.settings.storageDomain = window.location.hostname; // (Sets the domain for your deployment so that visitors can navigate subdomains during a chat session)

  // Settings for Chat
  // window.embedded_svc.settings.directToButtonRouting = prechatFormData();

  // Dynamically changes the button ID based on what the visitor enters in the pre-chat form. // Returns a valid button ID. //}

  // window.embedded_svc.settings.prepopulatedPrechatFields = {}; // Sets the auto-population of pre-chat form fields
  // window.embedded_svc.settings.fallbackRouting = []; // An array of button IDs, user IDs, or userId_buttonId
  // window.embedded_svc.settings.offlineSupportMinimizedText = 'Contact Us'; // (Defaults to Contact Us)

  window.embedded_svc.settings.enabledFeatures = ['LiveAgent'];
  window.embedded_svc.settings.entryFeature = 'LiveAgent';

  window.embedded_svc.init(
    config.baseUrl,
    config.supportUrl,
    gslbBaseURL,
    config.id,
    config.name,

    {
      baseLiveAgentContentURL: config.baseLiveAgentContentURL,
      deploymentId: config.deploymentId,
      buttonId: config.buttonId,
      baseLiveAgentURL: config.baseLiveAgentURL,
      eswLiveAgentDevName: config.eswLiveAgentDevName,
      isOfflineSupportEnabled: config.isOfflineSupportEnabled
    }
  );
};

export default initESW;
