import React from 'react';
import PropTypes from 'prop-types';
import { Typography, withStyles } from '@material-ui/core';
import { Offers, OfferDetails } from '@onereader/offers';

const styles = () => ({
  moreOffers: {
    color: '#FEFEFE',
    fontFamily: 'TT Commons',
    fontSize: 30,
    fontStyle: 'normal',
    fontWeight: 700,
    lineHeight: '110%',
    marginBottom: 24
  },
  moreOffersWrapper: {
    margin: '50px 75px 75px 75px'
  },
  offerDetails: {
    margin: '10px 75px 0px 75px',
    paddingBottom: 50
  },
  offerDetailsWrapper: {
    background: '#05112A',
    height: 'auto',
    overflow: 'hidden',
    position: 'relative',
    width: '100%',
    zIndex: 1,
    '&:before': {
      top: -427,
      left: -158,
      content: '""',
      position: 'absolute',
      background: 'linear-gradient(0.25deg, rgba(255, 255, 255, 0) 9.71%, #FFFFFF 47.46%, #FFFFFF 62.57%, rgba(255, 255, 255, 0) 100.32%)',
      mixBlendMode: 'overlay',
      filter: 'opacity(0.325)',
      transform: 'rotate(-34.35deg)',
      width: 659,
      height: 671,
      zIndex: -1
    },
    '&:after': {
      bottom: -194,
      right: -42,
      content: '""',
      position: 'absolute',
      background: 'linear-gradient(0.25deg, rgba(255, 255, 255, 0) 9.71%, #FFFFFF 47.46%, #FFFFFF 62.57%, rgba(255, 255, 255, 0) 100.32%)',
      mixBlendMode: 'overlay',
      filter: 'opacity(0.325)',
      transform: 'rotate(-34.35deg)',
      width: 659,
      height: 671,
      zIndex: -1,
      '@media screen and (max-width: 1024px)': {
        right: -493,
        bottom: -80
      }
    }
  }
});

const OfferDetailsContainer = ({
  classes,
  columns,
  descriptionHTML,
  handleCancel,
  handleOfferClick,
  intl,
  id,
  OffersData,
  onRedeemClick,
  redeemLink,
  showRedeem,
  subTitle,
  thumbnailImage,
  title,
  titleImage
}) => (
  <div className={classes.offerDetailsContainer}>
    <div className={classes.offerDetailsWrapper}>
      <div className={classes.offerDetails}>
        <div className={classes.offerDetailsBefore} />
        <OfferDetails
          descriptionHTML={descriptionHTML}
          id={id}
          locale={intl.currentLanguage}
          onRedeemClick={onRedeemClick}
          redeemLink={redeemLink}
          showRedeem={showRedeem}
          subTitle={subTitle}
          thumbnailImage={thumbnailImage}
          title={title}
          titleImage={titleImage}
        />
        <div className={classes.offerDetailsAfter} />
      </div>
    </div>
    <div className={classes.moreOffersWrapper}>
      <Typography component="div" className={classes.moreOffers}>
        More Offers
      </Typography>
      <Offers
        columns={columns}
        handleOfferClick={handleOfferClick}
        offers={OffersData}
      />
    </div>
  </div>
);

OfferDetailsContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  columns: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.number
  ]),
  descriptionHTML: PropTypes.string,
  handleCancel: PropTypes.func,
  handleOfferClick: PropTypes.func,
  intl: PropTypes.object.isRequired,
  id: PropTypes.string,
  OffersData: PropTypes.array,
  onRedeemClick: PropTypes.func,
  redeemLink: PropTypes.string,
  showRedeem: PropTypes.bool,
  subTitle: PropTypes.string,
  thumbnailImage: PropTypes.string,
  title: PropTypes.string,
  titleImage: PropTypes.string
};

OfferDetailsContainer.defaultProps = {
  columns: {
    xs: 1, sm: 1, md: 2, lg: 3, xl: 3, xxl: 4
  },
  descriptionHTML: '',
  handleCancel: () => {},
  handleOfferClick: () => {},
  id: null,
  OffersData: [],
  onRedeemClick: () => {},
  redeemLink: '',
  showRedeem: false,
  subTitle: '',
  thumbnailImage: '',
  title: '',
  titleImage: ''
};

export default withStyles(styles)(OfferDetailsContainer);
