/**
 * Mobx model(s) related to Offer
 *
 * @file Media.js
 * @author Poornima N
 */

import { types } from 'mobx-state-tree';
/**
    * A mobx model for Offer
    *
    */
const Media = types.model(
    'Media',
    {
        type: types.maybeNull(types.string),
        source: types.maybeNull(types.string)
    }
);

export default Media;
