import Framework, { BaseService } from '@greenville/framework';

import CommonUitls from '../../../common/utils/CommonUtils';
import {
  OFFERS_ELIGIBILITY_REQUESTED, X_RULEGROUP_ID, X_TRANSACTION_ID, OFFERS_ELIGIBILITY_SUCCESS, OFFER_EVENT
} from '../../../common/constants';
import env from '../../../common/env';
import UserDetailsUtils from '../../../common/utils/UserDetailsUtils';
import OffersPlugin from '../index';

/**
* @Author Mohamed yasar arabath M
*/
// let inc = 1;
export default class OffersEligibility extends BaseService {
  constructor() {
    super(OFFERS_ELIGIBILITY_REQUESTED, 'OffersStatus');
    this.callBack = null;
    this.OffersPlugin = new OffersPlugin(this);
    this.showOffersUserDetailModal = false;
  }

  handleEvent(eventName, { userId, callBack, showOffersUserDetailModal = false }) {
    const headerParams = {
      headers: {
        [X_RULEGROUP_ID]: `${env.OFFERS_ELIGIBILITY_RULE_GROUP}`,
        [X_TRANSACTION_ID]: CommonUitls.getUUID()
      }
    };
    // const OffersEligibilityFetchUser = `/user/${userId}?authtype=user`;
    this.callBack = typeof callBack === 'function' ? callBack : null;
    this.showOffersUserDetailModal = showOffersUserDetailModal;
    return this.getClient('offereligibility').get(`/eligibility/user/${userId}?authtype=user`, headerParams);
  }

  handleResponse(response) {
    let hasEligiblity = false;
    const { data } = response;
    const enableRuleIdOverride = this.showOffersUserDetailModal;
    // Testing purpose will be deleted once API is deployed
    // const enableRuleIdOverride = true;
    // if (inc === 1 && enableRuleIdOverride) {
    //   data.reasonCode = 'R01';
    //   data.studyLevel = null;
    //   // data.dob = '03/31/1986';
    //   data.userCountry = null;
    //   data.ruleId = 'PAPP07';
    //   data.reasonCode = 'R02';
    // }

    if (data && data.eligiblity) {
      const {
        eligiblity, studyLevel, dob, userCountry
      } = data;
      hasEligiblity = eligiblity.some(({ type, value }) => (type === 'DISPLAY_OFFERS' && value));

      // storing initial details received from elibility response
      Framework.getStoreRegistry().getStore('userdetails').setIntialDetails({
        studyLevel: studyLevel || null,
        dob: dob || null,
        userCountry: userCountry || null
      });

      if (data.ruleId) {
        Framework.getStoreRegistry().getStore('userdetails').setRuleId(data.ruleId);
      }

      if (data.reasonCode) {
        Framework.getStoreRegistry().getStore('userdetails').setReasonCode(data.reasonCode);
      }
      // Temporary fix from UI as there are constrain in the API
      if (
        enableRuleIdOverride
        && (data.ruleId && (data.ruleId === 'PAPP07' || data.ruleId === 'PAPP08' || data.ruleId === 'PAPP10'))
      ) {
        hasEligiblity = true;
        const displayofferIndex = eligiblity.findIndex((obj => obj.type === 'DISPLAY_OFFERS'));
        eligiblity[displayofferIndex].value = true;
      }
      Framework.getStoreRegistry().getStore('offers').setOffersEligibility(eligiblity);
    }
    if (!hasEligiblity && Framework.getStoreRegistry().getStore('offers').showUserDetails) {
      Framework.getStoreRegistry().getStore('userdetails').setError(true);
      Framework.getStoreRegistry().getStore('userdetails').setReasonCode('R100');
    }

    // Based on API contract the reason code will be present if there is any field missing
    if (enableRuleIdOverride
        && data && data.ruleId && (data.ruleId === 'PAPP07' || data.ruleId === 'PAPP08' || data.ruleId === 'PAPP10')
          && hasEligiblity) {
      Framework.getStoreRegistry().getStore('offers').setShowUserDetails(true, data.reasonCode);

      if (data.reasonCode === 'R99') {
        Framework.getStoreRegistry().getStore('userdetails').setError(true);
        Framework.getStoreRegistry().getStore('userdetails').setReasonCode(data.reasonCode);
      } else {
        Framework.getStoreRegistry().getStore('userdetails').setIntialDetails({
          studyLevel: data.studyLevel,
          dob: data.dob,
          userCountry: data.userCountry
        });
        Framework.getStoreRegistry().getStore('userdetails').setMonthList(UserDetailsUtils.getMonthList());
        Framework.getStoreRegistry().getStore('userdetails').setYearList(UserDetailsUtils.getYearList());

        if (data.dob) {
          let dateOfBirth = data.dob;
          dateOfBirth = dateOfBirth.split('/');

          Framework.getStoreRegistry().getStore('userdetails').setBirthMonth(dateOfBirth[0]);
          Framework.getStoreRegistry().getStore('userdetails').setBirthYear(dateOfBirth[2]);
        }
      }
    } else {
      Framework.getStoreRegistry().getStore('offers').setShowUserDetails(false, '');
    }
    // triggering EVENT ID 385 GA event in case hasEligiblity = false
    if (!hasEligiblity) {
      const { OFFERS_PAGE_VIEW_ACTION } = OFFER_EVENT;
      const customFields = this.OffersPlugin.generateCustomGAField();
      this.OffersPlugin.offersDispatchGAEvent(OFFERS_PAGE_VIEW_ACTION, '', customFields);
    }


    // Testing purpose will be deleted once API is deployed
    // if (enableRuleIdOverride) inc += 1;

    if (this.callBack) {
      this.callBack(hasEligiblity);
    }
    Framework.getEventManager().publish(OFFERS_ELIGIBILITY_SUCCESS, hasEligiblity);
  }

  handleError(error) {
    if (this.callBack) {
      this.callBack(null);
    }
    Framework.getStoreRegistry().getStore('offers').setError();
    console.log(error, 'OffersEligibility service error');
  }
}
