import React, { PureComponent, Fragment, Suspense } from 'react';
import ReactDOMServer from 'react-dom/server';
import PropTypes from 'prop-types';
import {
  CircularProgress,
  IconButton
} from '@material-ui/core';
import { FormattedMessage } from 'react-intl';
import { inject, observer } from 'mobx-react';
import { getSnapshot } from 'mobx-state-tree';
import Framework, { LoadingHandler, shapes } from '@greenville/framework';
import { EventProvider } from '@aquila/core';
import axios from 'axios';
import { withLDConsumer } from 'launchdarkly-react-client-sdk';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import CloseIcon from '@material-ui/icons/Close';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Alert from '@material-ui/lab/Alert';

import { OffersLink, OffersSidePanel } from '@onereader/offers';

import OffersPlugin from '../offers';
import OffersUtils from '../../common/utils/OffersUtils';
import Offers from '../offers/model/Offers';
import AppcuesUnboxing from '../../common/utils/AppcuesUnboxing';
import env from '../../common/env';
import PathUtils from '../../common/utils/PathUtils';
import Product from './model/Product';
import Asset from '../courses/model/Asset';
import ProductToken from './model/ProductToken';
import AuthHandler from '../../common/AuthHandler';
import HotspotUtils from '../../common/utils/HotspotUtils';
