/**
  * Offers Plugin
  *
  * @author Mohamed yasar arabath M
  */
import { getSnapshot } from 'mobx-state-tree';

import Framework from '@greenville/framework';
import CommonUtils from '../../common/utils/CommonUtils';
import OffersUtils from '../../common/utils/OffersUtils';
import { OFFER_EVENT } from '../../common/constants';

export default class OffersPlugin {
  constructor(context) {
    this.OffersContext = context;
    this.offersFiller = {
      description: 'We’ve teamed up with partners to bring Pearson+ members like you exclusive deals and special offers tailored to the college lifestyle.',
      title: 'What are Offers?'
    };
    this.LDConnect = null;
    this.LDConnectFail = null;
    this.UserRoleConnect = null;
    this.UserRoleConnectFail = null;
    this.LDFetched = false;
    this.userRoleFetched = false;
    this.isStudent = false;
    this.onShowOffersCallBack = null;
  }

  /**
 * get asynchronize function call success
 *
 * @param {string} page - AUTHHOME/EREADER
 * @param {function} errorHandle - optional errorHandle callback
 */

  init = (page, errorHandle = () => {}) => {
    const { offers } = this.OffersContext.props;
    const connectors = [
      new Promise((LDresolver, LDReject) => {
        this.LDConnect = LDresolver;
        this.LDConnectFail = LDReject;
      }),
      new Promise((userRoleresolver, userRoleresolverReject) => {
        this.UserRoleConnect = userRoleresolver;
        this.UserRoleConnectFail = userRoleresolverReject;
      })
    ];
    Promise.all(connectors).then(([flags, isStudent]) => {
      if (this.LDFetched && this.userRoleFetched) {
        const hasShowsOffers = OffersUtils.showOffers(page, flags, isStudent);
        this.setShowsOffers(hasShowsOffers);
        if (hasShowsOffers) {
          this.getOffers(flags.showOffersUserDetailModal);
          // check wheather plugin having that state from inhertaince class
          if (this.OffersContext.state) {
            const { offerHeaderLinkTitle, offerDrawerTitle } = this.OffersContext.state;
            if (offerHeaderLinkTitle !== undefined && offerDrawerTitle !== undefined) {
              this.setConfigureText(flags, isStudent);
            }
          }
        }
      }
    }).catch((error) => {
      console.log(error, 'offers error');
      offers.setError();
      errorHandle(error);
    });
  }

  /**
 * Handles set has shown offers
 *
 * @param {bool} hasShowsOffers
 */

   setShowsOffers = (hasShowsOffers) => {
     const hasCallback = this.onShowOffersCallBack;
     if (hasCallback) {
       hasCallback(hasShowsOffers);
     }
   }

   /**
 * on shown/hide offers callbacks
 *
 * @param {bool} hasShowsOffers
 */

   onShowOffers = (callback) => {
     let exec = null;
     if (typeof callback === 'function') {
       exec = callback;
     }

     this.onShowOffersCallBack = exec;
   }

   /**
 * Handles set LD flags success
 *
 * @param {object} flags
 */

  setLDFetched = (flags) => {
    if (!this.LDFetched) {
      this.LDFetched = true;
      this.LDConnect(flags);
    }
  }

  /**
 * Handles set LD flags fails
 *
 * @param {object} flags
 */

  LDFail = (error) => {
    this.LDConnectFail(error);
  }

  /**
 * Handles set user role success
 *
 * @param {*} books
 */

  setUserRoleFetched = (books) => {
    if (!this.userRoleFetched) {
      this.userRoleFetched = true;
      const isStudent = CommonUtils.getUserRole(books);
      this.isStudent = isStudent;
      this.UserRoleConnect(isStudent);
    }
  }

  /**
 * Handles set user role fail
 *
 * @param {*} error
 */

  userRoleFail = (error) => {
    this.UserRoleConnectFail(error);
  }

  /**
 * Handles to get offers
 */

  getOffers = (showOffersUserDetailModal = false) => {
    const { offers } = this.OffersContext.props;
    offers.fetchOffersEligibility(showOffersUserDetailModal).then((hasEligiblity) => {
      if (hasEligiblity) {
        offers.fetchOffers();
      }
    });
  }

  /**
 * get A/B Launch Darkly flag to configure text in eReader
 */

   setConfigureText = (flags, isStudent) => {
     const { header, drawer } = flags['offers-configurable-ereader-text'];
     const showExclusiveOffersLinkString = flags['show-exclusive-offers-link-string'];
     if (showExclusiveOffersLinkString) {
       const offerHeaderLinkTitle = this.getTitle(header, isStudent) || '';
       this.OffersContext.setState({ offerHeaderLinkTitle });
     }
     const offerDrawerTitle = this.getTitle(drawer, isStudent);
     this.OffersContext.setState({ offerDrawerTitle });
   }

   /**
   * get LD config string based on student/ instructor
   * @param {array} list
   * @param {boolean} isStudent
   */

   getTitle = (list, isStudent) => {
     let title = null;
     if (list && list.length > 0) {
       const content = list.filter(config => config.isStudent === isStudent).pop();
       if (content && content.title) {
         title = content.title;
       }
     }
     return title;
   }

   /**
   * Handle Offers in Side panel GA events
   * @param {string} offerId
   * @param {string} locationInApp
   */
   offerActionGAEvent = (offerId, locationInApp, overrideCustomFields = {}) => {
     const { CARD_CLICK_EVENT_ACTION } = OFFER_EVENT;
     const { offers } = this.OffersContext.props;
     const {
       design_id,
       title: eventLabel,
       redemption_link: target_url,
       subtitle: partner_offer
     } = OffersUtils.getSelectedOffer(getSnapshot(offers.offers_list), offerId);
     const additionalCustomField = this.generateCustomGAField();
     const customFields = {
       design_id,
       partner_id: eventLabel,
       location_in_app: locationInApp,
       target_url,
       partner_offer,
       ...additionalCustomField,
       ...overrideCustomFields
     };
     this.offersDispatchGAEvent(
       CARD_CLICK_EVENT_ACTION,
       eventLabel,
       customFields
     );
   }

  /**
   * Handle GA events for offers
   * @param {string} eventAction
   * @param {string} eventLabel
   * @param {object} customFields
   */
  offersDispatchGAEvent = (eventAction, eventLabel, customFields = null) => {
    const { EVENT_CATEGORY, EVENT_NAME } = OFFER_EVENT;
    CommonUtils.dispatchGaEvent(EVENT_CATEGORY, EVENT_NAME, eventAction, eventLabel, customFields);
  }

  /**
   * Handle header offer Link events
   * @param {*} event
  */

  handleOfferLinkClick = (event) => {
    const { offers } = this.OffersContext.props;
    const { EXCLUSIVE_CLICK_EVENT_ACTION, OFFERS_ICON_CLICK_TEXT } = OFFER_EVENT;

    if (event && event.target) {
      const eventLabel = event.target.innerText || OFFERS_ICON_CLICK_TEXT;
      const additionalCustomField = this.generateCustomGAField();
      this.offersDispatchGAEvent(EXCLUSIVE_CLICK_EVENT_ACTION, eventLabel, additionalCustomField);
    }
    if (offers.showUserDetails) {
      this.OffersContext.setState({
        showUserDetails: true
      });
    } else {
      this.OffersContext.setState({ showOffersDrawer: true });
    }
  }
  /**
   * Handle Redeem link from offerDetails
   * @param {*} event
   * @param {*} offerId
  */

   handleRedeemClick = (event, offerId) => {
     const { REDEEM_CLICK_EVENT_ACTION } = OFFER_EVENT;
     const { offers } = this.OffersContext.props;
     const { title: partnerId } = OffersUtils.getSelectedOffer(getSnapshot(offers.offers_list), offerId);
     if (event && event.target) {
       const eventLabel = event.target.innerText;
       const customFields = {
         partner_id: partnerId
       };

       this.offersDispatchGAEvent(REDEEM_CLICK_EVENT_ACTION, eventLabel, customFields);
     }
   }

   /**
   * Generating customAdditionalField's for offer GA event
   */

    generateCustomGAField = () => {
      let customFields = {};
      const {
        intialDetails, ruleId, reasonCode
      } = Framework.getStoreRegistry().getStore('userdetails');
      if (intialDetails !== undefined && intialDetails !== null) {
        const { dob, studyLevel, userCountry } = intialDetails;
        let formattedDob = null;
        if (dob !== undefined && dob !== null) {
          const dobArr = dob.split('/');
          formattedDob = (dobArr.length === 3) ? `${dobArr[0]}/${dobArr[2]}` : null;
        }
        customFields = {
          country: userCountry, month_and_year_of_birth: formattedDob, grade: studyLevel, rule_id: ruleId, reason_code: reasonCode || null
        };
      } else {
        customFields = {
          country: null, month_and_year_of_birth: null, grade: null, rule_id: ruleId, reason_code: reasonCode || null
        };
      }
      return customFields;
    }
}
