import React, { Suspense } from 'react';
import { CircularProgress } from '@material-ui/core';

import OfferDetailsContainer from './OfferDetailsContainer';


export default {
  path: [
    '/offer/:container/:id/(products)?/:productId?/(bundles)?/:bundleid?/offerDetails/:offerId'
  ],
  exact: true,
  services: [],
  stores: [],
  render: props => (
    <Suspense fallback={<CircularProgress className="circleStyle" />}>
      <OfferDetailsContainer {...props} />
    </Suspense>
  )
};
