import Framework, { BaseService } from '@greenville/framework';
import * as constants from '../../../common/constants';
import CommonUtils from '../../../common/utils/CommonUtils';

/**
 * A service to fetch the course details.
 * @author Hari Gangadharan
 */
export default class CourseService extends BaseService {
  constructor() {
    super(constants.COURSE_SYNC_REQUESTED, 'courseStatus');
  }

  handleEvent(eventName, event) {
    const getPath = event.productId ? `/products/${event.productId}` : '';
    let headerParams = null;
    if (event.bundleId) {
      headerParams = {
        headers: {
          'bundle-id': event.bundleId
        }
      };
    }
    return this.getClient('marin').get(`/courses/${event.courseId}${getPath}`, headerParams);
  }

  handleResponse(response, event) { // eslint-disable-line class-methods-use-this
    const productData = response.data;
    Framework.getStoreRegistry().getStore('course').set(response.data, event.bundleId);
  }
}
