/**
 * Related to Ereader unboxing flow
 *
 * @file AppcuesUnboxing.js
 * @author Mohamed yasar arabath M
 */

import AppcuesUtils from './AppcuesUtils';
import { APPCUES_TRACK_EVENT, APPCUES_EREADER_UNBOXING_FLOW } from '../constants';

class AppcuesUnboxing {
  constructor(context) {
    this.appcuesUnboxingContext = context;
    this.offerConnect = null;
    this.appCuesConnect = null;
    this.userPreferenceConnect = null;
  }

  init = () => {
    const connectors = [
      new Promise((offerResolver) => {
        this.offerConnect = offerResolver;
      }),
      new Promise((appcuesResolver) => {
        this.appCuesConnect = appcuesResolver;
      }),
      new Promise((userPreferenceResolver) => {
        this.userPreferenceConnect = userPreferenceResolver;
      })
    ];
    Promise.all(connectors).then(([offersEligibility, appCueStarted, userPreference]) => {
      const isFirstLogin = userPreference && userPreference.userActivity && userPreference.userActivity.isFirstLogin;
      if (appCueStarted && (isFirstLogin || typeof isFirstLogin === 'undefined')) {
        const { product, course } = this.appcuesUnboxingContext.props;
        const userType = product ? product.getBusinessModelCode() : course.getProduct().getBusinessModelCode();
        const eventData = {
          eventName: APPCUES_EREADER_UNBOXING_FLOW,
          eventProperties: {
            userType,
            offersEligibility
          }
        };
        AppcuesUtils.handleEvent(APPCUES_TRACK_EVENT, null, eventData);
      }
    });
  }

  setOfferEligiblity = (hasEligiblity) => {
    this.offerConnect(hasEligiblity);
  }

  setAppCuesInit = () => {
    this.appCuesConnect(true);
  }

  setUserPreference = (data) => {
    this.userPreferenceConnect(data);
  }
}

export default AppcuesUnboxing;
