/**
 * MondlyLanguageContainer for Mondly Language comp
 *
 * @file MondlyLanguageContainer.jsx
 * @author Shyam Sunder
 */

import React, { Fragment, memo } from 'react';
import PropTypes from 'prop-types';
import { Box, withStyles } from '@material-ui/core';
import { LanguagesGrid } from '@study/components';
import '@study/components/dist/index.css';
import './mondly.scss';

const MondlyStyle = () => ({
  mondlyContainer: {
    padding: '26px 64px 22px 64px',
    '@media screen and (max-width: 499px)': {
      margin: '26px 16px 22px 16px'
    },
    '@media screen and (min-width:500px) and (max-width: 1439px)': {
      padding: '26px 32px 22px 32px'
    }
  }
});

const MondlyLanguageContainer = ({
   userDetail, env, locale, classes
}) => {
  const { id: userId, token } = userDetail;
  return (
    <Fragment>
      <Box className={classes.mondlyContainer} id="mondly-etext" data-title="Mondly">
        <LanguagesGrid 
          getUserCallback={() => ({ userId, token })} 
          env={env} 
          locale={locale} 
        />
      </Box>
    </Fragment>
  );
};
MondlyLanguageContainer.propTypes = {
  containerOrder: PropTypes.number,
  userDetail: PropTypes.object.isRequired,
  env: PropTypes.oneOf(['dev', 'qa', 'stg', 'stage', 'perf', 'ppe', 'prod']).isRequired,
  locale: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired
};
export default memo(withStyles(MondlyStyle)(MondlyLanguageContainer));
