import Framework, { BaseService } from '@greenville/framework';

import {
  OFFERS_CONTENT_REQUESTED,
  OFFERS_CONTENT_SUCCESS
} from '../../../common/constants';

/**
* @Author Mohamed yasar arabath M
*/

export default class OffersContent extends BaseService {
  constructor() {
    super(OFFERS_CONTENT_REQUESTED);
    this.error = false;
  }

  handleEvent() {
    let hasRedeemtion = false;
    const eligiblity = Framework.getStoreRegistry().getStore('offers').eligiblity;
    hasRedeemtion = eligiblity.some(({ type, value }) => (type === 'REDEMPTION' && value));

    return this.getClient('marin').get(`/offers?activeEntitlement=${hasRedeemtion}`);
  }

  handleResponse(response) {
    this.error = false;
    const { data } = response;
    if (data && data.offers_list) {
      Framework.getStoreRegistry().getStore('offers').setOffers(data.offers_list);
    }
    Framework.getEventManager().publish(OFFERS_CONTENT_SUCCESS);
  }

  handleError(error) {
    this.error = true;
    Framework.getStoreRegistry().getStore('offers').setError();
    console.log(error, 'Offers Content service error');
  }
}
