/**
 * Custom Scroll Container
 *
 * @file CustomScrollContainer.jsx
 * @author Srinivasan Bakthavatchalam
 * @since 0.0.1
 */
import React from 'react';
import PropTypes from 'prop-types';
import { Scrollbars } from 'react-custom-scrollbars';
import { withStyles } from '@material-ui/core';

const styles = () => ({
  bodyWrapper: {
    flex: '1 1 auto',
    height: '100%',
    width: '100%'
  },
  verticalThumb: {
    backgroundColor: '#C4C4C4',
    borderRadius: 6,
    zIndex: 1210
  }
});

/**
 * Customizing the render view of Scrollbars component
 *
 */
const ScrollBarsRenderView = ({
  style,
  ...props
}) => {
  const customStyle = {
    overscrollBehavior: 'contain' // this is to overcome the `scroll chaining`
  };

  return (
    <div
      {...props} // eslint-disable-line react/jsx-props-no-spreading
      style={{ ...style, ...customStyle }}
    />
  );
};

ScrollBarsRenderView.propTypes = {
  style: PropTypes.object
};

ScrollBarsRenderView.defaultProps = {
  style: {}
};

const CustomScrollContainer = ({
  children,
  classes
}) => (
  <div className={classes.bodyWrapper}>
    <Scrollbars
      // This will activate auto hide
      autoHide
      // Hide delay in ms
      autoHideTimeout={1000}
      id="or-drop-down-scroll"
      // Duration for hide animation in ms.
      autoHideDuration={200}
      className="or-drop-down-scrollbar"
    // renderThumbHorizontal={scrollProps => <div {...scrollProps} className="right-drawer-thumb-horizontal" />} // eslint-disable-line react/jsx-props-no-spreading
      renderThumbVertical={scrollProps => <div {...scrollProps} className={classes.verticalThumb} />} // eslint-disable-line react/jsx-props-no-spreading
      renderView={ScrollBarsRenderView}
      autoHeight
      autoHeightMin={0}
      autoHeightMax="222px"
      universal
    >
      <div className="or-drawer-body-container">
        {children}
      </div>
    </Scrollbars>
  </div>
);

CustomScrollContainer.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node
  ]),
  classes: PropTypes.object.isRequired
};

CustomScrollContainer.defaultProps = {
  children: null
};

export default withStyles(styles)(CustomScrollContainer);
