/**
 * User Details
 *
 * @file UserDetails.jsx
 * @author Srinivasan Bakthavatchalam
 */

import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme, withStyles } from '@material-ui/core/styles';
import { getSnapshot } from 'mobx-state-tree';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import { Icon } from '@greenville/mui-theme';
import { CircularProgress, IconButton } from '@material-ui/core';

import { inject, Observer } from 'mobx-react';
import Framework, { shapes } from '@greenville/framework';

import * as UserDetailsModel from './model/UserDetails';

import ErrorScreen from './components/ErrorScreen/ErrorScreen';
import UserDetailsForm from './components/UserDetailForm/UserDetailForm';
import Loading from './components/Loading/Loading';

import UserDetailsStyles from './UserDetailsStyles';
import ApiError from '../../common/icons/ApiError.png';


const UserDetails = ({
  classes,
  userdetails,
  handleContactSupport,
  handleUserDetailClose,
  isInstructor
}) => {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('xs'));
  const language = Framework.getStoreRegistry().getStore('language');
  /**
   * Handles user detial form submit
   */
  const handleSubmit = () => {
    userdetails.updateUserDetails();
  };
  const inEligibleUserMsg = {
    headingText: language.getMessage('offers.INELIGIBLE_HEADING'),
    subHeadingText: language.getMessage('offers.INELIGIBLE_SUBHEADING'),
    imageSource: ApiError
  };
  const apiErrorMsg = {
    headingText: language.getMessage('offers.API_ERROR_HEADING'),
    subHeadingText: language.getMessage('offers.API_ERROR_SUBHEADING'),
    imageSource: ApiError
  };
  /**
   * Handle modal close
   */
  const handleClose = () => {
    userdetails.setIntialValues();
    userdetails.setError(false);
    userdetails.setIsUpdateApiInprogress(false);
    userdetails.setReasonCode('');
    handleUserDetailClose();
  };

  useEffect(() => {
    if (!userdetails.isCountryListFetched) userdetails.fetchCountryList();
    if (!userdetails.isGradeListFetched) userdetails.fetchGradeList();
  }, []);
  return (
    <Observer>
      {() => (
        <div>
          <Dialog
            classes={{
              paper: classes.dialogPaper,
              root: classes.root
            }}
            fullScreen={fullScreen}
            open
            scroll="body"
            aria-label={language.getMessage('offers.POPUP_HEADING')}
          >
            {!userdetails.isApiError && (!userdetails.isCountryListFetched || !userdetails.isGradeListFetched)
              ? <div className={classes.loadingStyled}><CircularProgress /></div>
              : (
                <>
                  <MuiDialogTitle className={classes.dialogTitle}>
                    <IconButton
                      aria-label="close"
                      className={classes.iconColor}
                      disabled={userdetails.isUpdateApiInprogress}
                      onClick={handleClose}
                    >
                      <Icon name="Remove" fontSize="large" />
                    </IconButton>
                  </MuiDialogTitle>
                  <DialogContent classes={{ root: classes.contentRoot }}>
                    <DialogContentText>
                      {
                      (userdetails.isApiError)
                        ? (
                          <ErrorScreen
                            headingText={userdetails.reasonCode === 'R100'
                              ? inEligibleUserMsg.headingText : apiErrorMsg.headingText}
                            subHeadingText={userdetails.reasonCode === 'R100'
                              ? inEligibleUserMsg.subHeadingText : apiErrorMsg.subHeadingText}
                            imageSource={userdetails.reasonCode === 'R100'
                              ? inEligibleUserMsg.imageSource : apiErrorMsg.imageSource}
                          />
                        )
                        : (
                          <UserDetailsForm
                            country={userdetails.country}
                            countryList={getSnapshot(userdetails.listOfCountries)}
                            disabled={userdetails.isUpdateApiInprogress}
                            grade={userdetails.grade}
                            gradeList={getSnapshot(userdetails.listOfGrades)}
                            isInstructor={isInstructor}
                            language={language}
                            month={userdetails.birthMonth}
                            months={getSnapshot(userdetails.listOfMonths)}
                            setBirthMonth={userdetails.setBirthMonth}
                            setBirthYear={userdetails.setBirthYear}
                            setCountry={userdetails.setCountry}
                            setGrade={userdetails.setGrade}
                            year={userdetails.birthYear}
                            years={getSnapshot(userdetails.listOfyears)}
                          />
                        )
                    }
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions classes={{ root: classes.actionContainer }}>
                    {/* {(!userdetails.isApiError || (userdetails.isApiError && userdetails.reasonCode === 'R99')) */}

                    { !userdetails.isApiError
                  && (
                  <Button
                    classes={{
                      disabled: userdetails.isUpdateApiInprogress
                        ? classes.buttonDisabledInprogress : classes.buttonDisabled,
                      focusVisible: classes.focusVisible
                    }}
                    className={classes.nextButton}
                    color="primary"
                    disabled={!userdetails.enableSubmit || userdetails.isUpdateApiInprogress}
                    onClick={handleSubmit}
                  >
                    { userdetails.isUpdateApiInprogress ? <Loading /> : 'Submit' }
                  </Button>
                  )}
                    {(userdetails.isApiError && userdetails.reasonCode === 'R99') && (
                    <Button
                      classes={{
                        disabled: classes.buttonDisabled,
                        focusVisible: classes.focusVisible
                      }}
                      className={classes.nextButton}
                      color="primary"
                      onClick={handleContactSupport}
                    >
                      {language.getMessage('offers.CONTACT_SUPPORT')}
                    </Button>
                    )}
                  </DialogActions>
                </>
              )
}
          </Dialog>
        </div>
      )
    }
    </Observer>
  );
};

UserDetails.propTypes = {
  classes: PropTypes.object.isRequired,
  userdetails: shapes.modelOf(UserDetailsModel).isRequired,
  handleContactSupport: PropTypes.func,
  handleUserDetailClose: PropTypes.func,
  isInstructor: PropTypes.bool
};

UserDetails.defaultProps = {
  handleContactSupport: () => {},
  handleUserDetailClose: () => {},
  isInstructor: false
};

export default inject('userdetails')(withStyles(UserDetailsStyles)(UserDetails));
