/**
 * ChannelCarouselContainer for ChannelCarousel comp
 *
 * @file OffersContainer.jsx
 * @author Vignesh Selvam
 */

import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { withStyles, Typography } from '@material-ui/core';
import { Offers } from '@onereader/offers';
import OfferDetailsContainer from './OfferDetailsContainer';
import * as constants from '../../common/constants';
import './offers.scss';

const OffersContainer = ({
  offers, offersSubtitle, handleOfferClick, showOfferDetails, selectedOfferDetails, handleRedeemClick, showRedeem, intl
}) => (
  !showOfferDetails ? (
    <Fragment>
      <Typography variant="h3" component="h2" className="offers-title offers-container">
                Offers
      </Typography>
      <Typography variant="subtitle2" component="p" className="offers-subtitle offers-container">
        {offersSubtitle}
      </Typography>
      <div>
        <Offers
          offers={offers}
          handleOfferClick={id => handleOfferClick(id, constants.OFFER_LOCATION.AUTHHOME)}
        />
      </div>
    </Fragment>
  )
    : (
      <Fragment>
        <OfferDetailsContainer
          columns={{
            xs: 1, sm: 1, md: 2, lg: 3, xl: 3, xxl: 4
          }}
          descriptionHTML={selectedOfferDetails.descriptionHTML}
          redeemLink={selectedOfferDetails.redeemLink}
          OffersData={offers}
          onRedeemClick={handleRedeemClick}
          showRedeem={showRedeem}
          subTitle={selectedOfferDetails.subTitle}
          thumbnailImage={selectedOfferDetails.thumbnailImage}
          title={selectedOfferDetails.title}
          titleImage={selectedOfferDetails.titleImage}
          handleOfferClick={id => handleOfferClick(id, constants.OFFER_LOCATION.OFFER_DETAILS)}
          id={selectedOfferDetails.id}
          intl={intl}
          handleCancel={() => {}}
        />
      </Fragment>
    ));

OffersContainer.defaultProps = {
  selectedOfferDetails: {},
  handleRedeemClick: () => {},
  showRedeem: false
};

OffersContainer.propTypes = {
  offers: PropTypes.object.isRequired,
  offersSubtitle: PropTypes.string.isRequired,
  handleOfferClick: PropTypes.func.isRequired,
  showOfferDetails: PropTypes.bool.isRequired,
  selectedOfferDetails: PropTypes.object,
  handleRedeemClick: PropTypes.func,
  showRedeem: PropTypes.bool,
  intl: PropTypes.object.isRequired
};
export default withStyles()(OffersContainer);
