import Framework, { BaseService } from '@greenville/framework';
import * as constants from '../../../common/constants';

/**
 * @author Arish Kumar K
 */
export default class CustomTocService extends BaseService {
  constructor() {
    super(constants.RESET_TOC_REQUESTED, 'resetTocStatus');
  }

  handleEvent(eventName, event) {
    return this.getClient('prism').put('/contenttoc/v1/assets', event.tocData);
  }

  handleResponse(response, event) { // eslint-disable-line class-methods-use-this
    const courseContainerState = Framework.getStoreRegistry().getStore('courseContainerState');
    Framework.getEventManager().publish(constants.ASSET_REQUESTED, {
      productId: event.productId,
      courseId: event.courseId
    });
    courseContainerState.set({ showSuccessMsg: true });
  }

  handleError() { // eslint-disable-line class-methods-use-this
    Framework.getStoreRegistry().getStore('courseContainerState').set({ showSuccessMsg: false });
  }
}
