/**
 * Bookshelf module index file
 *
 * @file index.js
 * @author Saravanan Kandasamy
 */

import React, { Suspense } from 'react';
import { CircularProgress } from '@material-ui/core';

import Bookshelf from './model/Bookshelf';
import BookshelfService from './services/BookshelfService';
import LastLocation from './model/LastLocation';
import LastLocationService from './services/LastLocationService';

const BookshelfContainer = React.lazy(() => import('./BookshelfContainer'));

export default {
  path: '/bookshelf',
  exact: true,
  services: [
    new BookshelfService(),
    new LastLocationService()
  ],
  stores: [
    {
      name: 'bookshelf',
      spec: Bookshelf,
      saveLocally: false,
      initialState: {
        book_id: '',
        book_title: '',
        entitlement_source: '',
        description: '',
        url: '',
        cover_image_url: '',
        book_type: '',
        author: '',
        index_id: '',
        version: '',
        product_id: '',
        product_model: '',
        isbn: '',
        language: '',
        user_id: '',
        study_guide_available: false,
        audio_book_supported: '',
        courses: [{
          course_id: '',
          course_name: '',
          course_start_date: '',
          course_end_date: '',
          user_role: ''
        }],
        product_entitlements: {
          subscription_id: '',
          type: '',
          business_model_code: '',
          end_date: '',
          start_date: '',
          expired: false
        }
      }
    },
    {
      name: 'lastlocation',
      spec: LastLocation,
      saveLocally: false,
      initialState: {
        id: '',
        userId: '',
        bookId: '',
        lastActivity: '',
        tenantId: '',
        tenantKey: '',
        createdTime: '',
        courseId: '',
        locations: [
          {
            id: '',
            activity: '',
            type: '',
            displayTitle: '',
            updatedTime: ''
          }
        ]
      }
    }
  ],
  render: props => (
    <Suspense fallback={<CircularProgress className="circleStyle" />}>
      <BookshelfContainer {...props} />
    </Suspense>
  )
};
