/**
 * Mobx model(s) related to OfferDetail
 *
 * @file OfferDetail.js
 * @author Mohamed yasar arabath M
 */

import { types } from 'mobx-state-tree';
/**
     * A mobx model for offerDetail
     *
     */
const OfferDetail = types.model(
  'OfferDetail',
  {
    description: types.maybeNull(types.string),
    detail_image: types.maybeNull(types.string),
    thumbnail_image: types.maybeNull(types.string),
    title: types.maybeNull(types.string),
    subtitle: types.maybeNull(types.string)
  }
);

export default OfferDetail;
