import React from 'react';
import { render } from 'react-dom';
import { asyncWithLDProvider } from 'launchdarkly-react-client-sdk';

import App from './App';
import env from './common/env';
import newrelic from './modules/newrelic/newrelic';

(async () => {
  const LDProvider = await asyncWithLDProvider({
    clientSideID: env.LD_CLIENT_SIDE_ID,
    user: {
      key: env.LD_ANONYMOUS_USER_ID,
      anonymous: true
    },
    options: {
      hash: env.LD_ANONYMOUS_HASH
    }
  });
  render(
    <LDProvider>
      <App />
    </LDProvider>,
    document.getElementById('root')
  );
})();

if (env.NEWRELIC_ENABLED && window.location.hostname !== 'local-dev.pearson.com') {
  newrelic.init();
}
