/**
 * Bookshelf service
 *
 * @file BookshelfService.js
 * @author Infant Diana Sebastian
 */

import Framework, { BaseService } from '@greenville/framework';
import {
  BOOKSHELF_DATA_REQUESTED,
  LASTLOCATION_DATA_REQUESTED,
  INTEGRATED_SMS,
  INTEGRATED_RUMBA,
  BRONTE_EPUB,
  CG_RENEWALS,
  ACTIVE,
  PAUSED,
  CONTENT_TYPE_EPUB
} from '../../../common/constants';
import CommonUtils from '../../../common/utils/CommonUtils';
import ProductUtils from '../../../common/utils/ProductUtils';

export default class BookshelfService extends BaseService {
  constructor() {
    super(BOOKSHELF_DATA_REQUESTED, 'bookshelfStatus');
  }

  handleEvent() {
    return this.getClient('marin').get('/bookshelf');
  }

  handleResponse(response) { // eslint-disable-line class-methods-use-this
    const bookIds = [];
    const integratedBMC = [INTEGRATED_SMS, INTEGRATED_RUMBA, CG_RENEWALS];
    const booksFormed = [];
    const bookshelfData = {
      originalBooks: response.data,
      books: booksFormed
    };

    let active, activeBooks = [], inactiveBooks = [];
    const {
      gpsSubscriptions
    } = Framework.getStoreRegistry().getStore('user');

    if (gpsSubscriptions && gpsSubscriptions.length > 0) active = gpsSubscriptions.find(subscription => subscription.status === ACTIVE || subscription.status === PAUSED);
    const entitlementLevel = active && active.entitlementTier;

    bookshelfData.originalBooks && bookshelfData.originalBooks.length > 0 && bookshelfData.originalBooks.map(book => {
      bookIds.push(integratedBMC.includes(book.product_entitlements.business_model_code) ? book.book_id : book.product_id);
      const productModelName = ProductUtils.getProductModel(book.product_entitlements.business_model_code, entitlementLevel, false);
      const format = book.book_type && book.book_type === CONTENT_TYPE_EPUB ? BRONTE_EPUB : book.book_type;
      if (book.courses.length > 0) {
        book.courses.sort((a, b) => a.course_name && b.course_name && a.course_name.localeCompare(b.course_name));
        book.courses.forEach((course) => {
          booksFormed.push({
            ...book,
            title: book.book_title || course.course_name,
            book_title: course.course_name || book.book_title,
            course_id: course.course_id || book.book_id,
            course_expired: course.course_end_date === null ? book.product_entitlements.end_date < CommonUtils.getLocalIsoTime() : course.course_end_date < CommonUtils.getLocalIsoTime(),
            expired: course.course_end_date === null ? book.product_entitlements.end_date < CommonUtils.getLocalIsoTime() : book.product_entitlements.end_date < CommonUtils.getLocalIsoTime() && course.course_end_date < CommonUtils.getLocalIsoTime(),
            courses: [course],
            product_model_name: `${productModelName}, ${format}`
          })
        })
      }
      else {
        booksFormed.push({
          ...book,
          title: book.book_title,
          course_expired: false,
          expired: book.product_entitlements.end_date < CommonUtils.getLocalIsoTime(),
          product_model_name: `${productModelName}, ${format}`
        });
      }
    }
    );

    // event id 366 READ- 3977

    //Filter active Books

    activeBooks = bookshelfData && bookshelfData.books && bookshelfData.books.filter(book => (
        book.product_entitlements &&
        !book.product_entitlements.expired && !book.course_expired
    ));

    //Filter Inactive Books
    inactiveBooks = bookshelfData && bookshelfData.books && bookshelfData.books.filter(book => book.product_entitlements
        && (book.product_entitlements.expired || book.course_expired));  
    
    bookshelfData.activeBooks = activeBooks; //adding custom fields for telemetry use
    bookshelfData.inactiveBooks = inactiveBooks; //adding custom fields for telemetry use
    
    if (bookIds.length > 0) {
      Framework.getEventManager().publish(LASTLOCATION_DATA_REQUESTED, { bookIds, bookshelfData });
    } else {
      Framework.getStoreRegistry().getStore('bookshelf').set(bookshelfData);
    }
  }
}
