/**
 * User Details Utils
 *
 * @file UserDetailsUtils.js
 * @author Srinivasan Bakthavatchalam
 */

export default class OffersUtils {
  /**
   * Get months option list
   * @returns
   */
  static getMonthList = () => {
    const startMonth = 1;
    const endMonth = 12;
    const months = [];

    for (let i = startMonth; i <= endMonth; i += 1) {
      months.push({
        label: i < 10 ? `0${i}` : i.toString(),
        value: i < 10 ? `0${i}` : i.toString()
      });
    }

    return months;
  };

  /**
   * Get year option list
   * @returns
   */
  static getYearList = () => {
    const startYear = 1940;
    const endYear = new Date().getFullYear();
    const years = [];

    for (let i = startYear; i <= endYear; i += 1) {
      years.push({
        label: i.toString(),
        value: i.toString()
      });
    }

    return years;
  };

  /**
   * Get the lable value pair based on the key passed
   *
   * @param {*} data
   * @param {*} label
   * @param {*} value
   * @returns
   */
  static getLabelValuePair = (data, label, value) => {
    const labelValuePair = [];

    data.map(option => labelValuePair.push({ label: option[label], value: option[value] }));

    return labelValuePair;
  };
}
