  /**
   * Gets the user type Pplus or non Pplus
   * returns the subcription status for Pplus user and
   * undefined for non Pplus
   *
   * @param {*} user
   * @returns
   */
  static getUserType(user) {
    let userType;
    if (user && user.gpsSubscriptions) {
      const GPSSubscriptionData = user.gpsSubscriptions.find(subscription => subscription.status === constants.ACTIVE ||
        subscription.status === constants.PAUSED);
      userType = GPSSubscriptionData && GPSSubscriptionData.status;
    }
    return userType;
  }

  /**
   * 
   * @param {Object} marqueeResponse 
   * return the filtered data for marquee container
   */
  static getMarqueeData = (marqueeResponse, userData, books, userFirstLogin, swaptitle, userLanguage) => {
    const marqueeData = [];
    const isTpi = PathUtils.getQueryParameterbyName('isTpi');
    if (!this.isEmpty(marqueeResponse) && marqueeResponse.marquee.slides.length > 0) {
      const userType = this.getUserType(userData);
      const isUserCountryUS = userData.homeCountryCode && userData.homeCountryCode.toLowerCase() === 'us';
      const PearsonPlusBooks = this.getProductByBMC(books, constants.DIRECT_TO_CONSUMER);
      const hasPearsonPlusBooks = PearsonPlusBooks.length > 0;
      const itemsToBeFiltered = ['welcome', 'welcomeback', 'welcomeNonEnglish', 'welcomebackNonEnglish', 'welcomeMLMEnglish', 'welcomeMLMNonEnglish', 'welcomebackMLMEnglish', 'welcomebackMLMNonEnglish', 'welcomebackUSStudentNotGhost', 'letsgetgoing', 'subpaused', 'wantmore', 'emptyscreen', 'upgradetomulti', 'genericempty', 'bookstoreupgrade', 'swaptitle', 'coderedeemed'];
      const totalSlides = [...marqueeResponse.marquee.slides];
      const firstSlidesArray = [];
      let firstSlide = [];
      const hasMultiEntitlementLevel = this.hasEntitlementLevel(userData, constants.ENTITLEMENTLEVEL_MULTI);
      const isStudent = this.getUserRole(books.books);
      const isFinancialAidedUser = CommonUtils.hasSubscriptions(userData, constants.CHANNEL_PARTNER);
      const isAllInActiveBooks = (books.books).every(
        (book => book.product_entitlements && (book.product_entitlements.expired || book.course_expired)));

      itemsToBeFiltered.map((index, key) => {
        const findIndexToRemvoe = totalSlides.findIndex(data => (data.id === index));
        const filterData = totalSlides.find(data => (data.id === index));

        if (filterData !== undefined) {
          firstSlidesArray.push(filterData);
          totalSlides.splice(findIndexToRemvoe, 1);
        }
      });

      if (userType === 'ACTIVE') {
        if (hasPearsonPlusBooks) { //mojo books
          //first login - welcome 
          //second or consecutive visit -- if multi, welcome back 
          //second or consecutive visit -- if single, welcome back + upgrade
          if (userFirstLogin) {
            if (isUserCountryUS) {
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcome'));
            }
            else {
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomeNonEnglish'));
            }
          } else if (hasMultiEntitlementLevel && !userFirstLogin) {
            if (isUserCountryUS) {
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomeback'));
            }
            else
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomebackNonEnglish'));
          } else if (!hasMultiEntitlementLevel && !userFirstLogin) {
            if (isFinancialAidedUser) {
              firstSlide = firstSlidesArray.find((data) => data.id === 'welcomeback'); //reverted and planning for sept15 release 
            } else {
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomeback')); //reverted and planning for sept15 release 
            }
          }
        } else {
          //lets get going
          if (isFinancialAidedUser) {
            firstSlide = firstSlidesArray.find((data) => data.id === 'coderedeemed');
          } else {
            firstSlide = firstSlidesArray.find((data) => data.id === 'letsgetgoing');
          }
        }
      } else if (userType === 'PAUSED') {
        //subscription paused
        firstSlide = firstSlidesArray.find((data) => data.id === 'subpaused');
      } else {
        if ((books.books).length > 0 && !isAllInActiveBooks) { //non-mojo books
          //first login - welcome 
          //second or consecutive visit 
          // if user is student // add welcome back and want more if country us, else if not us show only welcome back 
          // if user is ins, welcome back 
          if (userFirstLogin) {
            if (isUserCountryUS) {
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomeMLMEnglish'));
            }
            else {
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomeMLMNonEnglish'));
            }
          } else if (!userFirstLogin && !isStudent) {
            if (isUserCountryUS) {
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomebackMLMEnglish'));
            }
            else
              firstSlide = firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'welcomebackMLMNonEnglish'));
          } else if (!userFirstLogin && isStudent) {
            if (isUserCountryUS) {
              if (isTpi === undefined || isTpi !== 'Y') {
                firstSlide = firstSlidesArray.find((data) => data.id === 'welcomebackUSStudentNotGhost');
              }
              else {
                firstSlide = firstSlidesArray.find((data) => data.id === 'welcomebackMLMEnglish');

              }
            } else {
              firstSlide = firstSlidesArray.find((data) => data.id === 'welcomebackMLMNonEnglish');
            }
          }
        } else {
          // if country is us, add empty p+ promo what now
          // if country is not us, p.com promo //design or redirection is TBD
          if (isUserCountryUS) {
            firstSlide = firstSlidesArray.find((data) => data.id === 'emptyscreen');
          } else {
            firstSlide = firstSlidesArray.find((data) => data.id === 'genericempty');
          }
        }
      }
      if (firstSlide !== undefined) {
        marqueeData.push(firstSlide);
      }
      if (swaptitle && !isFinancialAidedUser) {
        totalSlides.push(firstSlidesArray.find((data) => (data.id !== 'undefined' && data.id === 'swaptitle')))
      }
      // marqueeData.push(marqueeResponse.marquee.slides.find((data) => data.id === 'channel'), marqueeResponse.marquee.slides.find((data) => data.id === 'mobilepromo'));

      return [...marqueeData, ...totalSlides];
    }

    return marqueeData;
  }
