  static initChatbotScript = (user) => {
    const bookshelfConfig = env.BOOKSHELF_CONFIG;
    if (this.getUserType(user)) {
      if (!window.embedded_svc) {
        const s = document.createElement('script');
        s.async = true;
        s.setAttribute('src', bookshelfConfig.chatbot.url);
        // eslint-disable-next-line func-names
        s.onload = function () { Chatbot(null, bookshelfConfig.chatbot); };
        document.body.appendChild(s);
      } else {
        const script1 = document.createElement('SCRIPT');
        script1.src = Chatbot('https://service.force.com', bookshelfConfig.chatbot);
        script1.async = true;
        document.body.appendChild(script1);
      }
      this.createSessionMirror();
    }
  }
  static createSessionMirror = () => {
    console.log(window.piSession.userId() + "Etext Pi User ID");
    const objectsReady = (window.toolbar instanceof Object) && (window.piSession instanceof Object);

    if (!objectsReady || window.isPiSessionUsageNotPossible) {
      return;
    }

    Object.defineProperty(window.toolbar, 'piToken', {
      get: () => {
        let token = null;
        window.piSession.getToken((status, rToken) => {
          token = rToken;
        });
        return token;
      }
    });
    Object.defineProperty(window.toolbar, 'piUserId', {
      get: () => { return window.piSession.userId(); }
    });
  }
  static pageContainerTracker() {
    const reactHandler = this;
    const orders = [];
    const containersNodeLists = document.querySelectorAll(constants.TRACKING_CONTAINERS);
    containersNodeLists.forEach((node, keyIndex) => {
      const position = constants.TRACKING_CONTAINERS.indexOf(`.${node.classList.value.split(' ')[0]}`);
      if (position !== -1) {
        orders[position] = keyIndex;
      }
    });
    const authHomeMap = constants.TRACKING_CONTAINERS.map((query, position) => {
      const value = document.querySelector(query);
      if (value && orders[position] !== null) {
        const customAttrOrder = document.createAttribute('data-order');
        customAttrOrder.value = orders[position] + 1;
        value.setAttributeNode(customAttrOrder);
      }
      return {
        query, value
      };
    });
    const callback = function (entries) {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          const personId = window.piSession.userId();
          const customFields = { event_value: entry.target.getAttribute('data-order'), personId, pageName: constants.AUTH_HOME_SCROLL_EVENT };
          CommonUtils.dispatchGaEvent(constants.AUTH_HOME_SCROLL_EVENT, constants.USER_CONTAINER, constants.USER_SEES_CONTAINER, entry.target.getAttribute('data-title'), customFields);
        }
      });
    };
    if (!this.observer) {
      const options = {
        root: null,
        rootMargin: '-200px 0px -200px 0px',
        threshold: 0
      };
      this.observer = new window.IntersectionObserver(callback, options);
    } else {
      this.observer.disconnect();
    }
    containersNodeLists.forEach((target) => {
      reactHandler.observer.observe(target);
    });
    return authHomeMap;
  }

  static isEmpty(obj) {
    return Object.keys(obj).length === 0;
  }

  static getBooksLength(books) {
    const booksLength = {
      activeBooksLength: 0,
      inactiveBooksLength: 0
    }
    const activeBooks = books && books.filter(book => (
      book.product_entitlements &&
      !book.product_entitlements.expired && !book.course_expired
    ));

    const inactiveBooks = books && books.filter(book => book.product_entitlements
      && (book.product_entitlements.expired || book.course_expired));
    booksLength.activeBooksLength = activeBooks.length;
    booksLength.inactiveBooksLength = inactiveBooks.length;

    return booksLength
  }

  static toString(value) {
    return (value && value !== 'n/a') ? value.toString() : null
  }
