import Framework, { BaseService } from '@greenville/framework';
import * as constants from '../../../common/constants';

/**
* Author Sarathkumar Venkatachalam
*/

export default class AssignmentService extends BaseService {
  constructor() {
    super(constants.ASSIGNMENT_REQUESTED, 'assignmentStatus');
  }

  handleEvent(eventName, event) {
    return this.getClient('marin').get(`/courses/${event.courseId}/assignments?expand=true`);
  }

  handleResponse(response) { // eslint-disable-line class-methods-use-this
    Framework.getStoreRegistry().getStore('assignments').set(response.data);
  }

  handleError(error) { // eslint-disable-line class-methods-use-this
    Framework.getStoreRegistry().getStore('assignments').setAssignmentsAsEmpty(error);
  }
}
