/**
 * Footer component
 *
 * @file Footer.jsx
 * @author Poornima N
 */

import React from 'react';
import { Typography, Link, List, ListItem, IconButton, withStyles } from '@material-ui/core';
import { injectIntl } from 'react-intl';

import MojoAppIcon from '../../assets/images/MojoAppIcon';
import FooterStyles from './FooterStyles';
import './Footer.scss';
import CommonUtils from '../../common/utils/CommonUtils';
import * as constants from '../../common/constants';

const Footer = ({
  intl, showAppIcon, menuList, classes, downloadApp
}) => {
  const handleFooterClick = (eventLabel, redirectUrl = '') => {
    const customFields = {
      event_value: eventLabel
    }
    CommonUtils.dispatchGaEvent(constants.FOOTER, constants.FOOTER_EVENT, 'Click Link', eventLabel, customFields) 

    if (redirectUrl) {
      window.open(redirectUrl, '_blank');
    }
  };

  return (
    <React.Fragment>
      <div className="footerDiv">
        <img className="footerIllustration" src='/assets/images/Pug.png' />
        <div className={classes.footercontainer}>
          <div className="downloadAppIcon">
            {showAppIcon &&
              <ListItem className={classes.footerApp}>
                <IconButton
                  id="download-app"
                  color="inherit"
                  className={classes.footerapp}
                  onClick={() => handleFooterClick('Download the mobile App', downloadApp.url)}
                >
                  <MojoAppIcon />
                  <Typography
                    component="p"
                    aria-label="Download the Mobile app"
                    className={classes.footerlinkApp}
                  >
                    Download the Mobile app
                  </Typography>
                </IconButton>
              </ListItem>
            }
          </div>
          <div className={classes.listDiv}>
            <List className={classes.footerList}>
              {menuList.map(({
                id, title, url, target, localeString
              }) =>
              (<ListItem className={`${classes[`footerListItem${id}`]}`} >
                <Link
                  onClick={() => handleFooterClick(title, url)}
                  color="primary"
                  target={target}
                  rel="noopener"
                  className={classes.footerlink}
                  aria-label={title}
                  tabindex="0"
                >
                  <Typography
                    component="p"
                    className={classes.footersection}
                  >
                    {title}
                  </Typography>
                </Link>
              </ListItem>)
              )}
            </List>
            <Typography
              component="p"
              className={`${classes.footeryear} ${classes.footercopyrightsection}`}
            >
              © 1996–2022 Pearson All rights reserved.
            </Typography>
          </div>
        </div>
        <div className="shape" />
      </div>
    </React.Fragment>

  );
};


export default withStyles(FooterStyles)(injectIntl(Footer));
