/**
 * Mobx model(s) related to UserDetails
 *
 * @file UserDetails.js
 * @author Srinivasan Bakthavatchalam
 */

import Framework from '@greenville/framework';
import { types } from 'mobx-state-tree';
import {
  COUNTRY_LIST_REQUESTED,
  FACULTY_OR_INSTRUCTOR,
  GRADE_LIST_REQUESTED,
  USER_DETAILS_UPDATE_REQUESTED
} from '../../../common/constants';

/**
 * A mobx model for Offers
 *
 */

const Options = types.model(
  'Options',
  {
    label: types.maybeNull(types.string),
    value: types.maybeNull(types.string)
  }
);

const IntialDetails = types.model(
  'IntialDetails',
  {
    dob: types.maybeNull(types.string),
    studyLevel: types.maybeNull(types.string),
    userCountry: types.maybeNull(types.string)
  }
);

const UserDetails = types.model(
  'UserDetails',
  {
    birthMonth: types.maybeNull(types.string),
    birthYear: types.maybeNull(types.string),
    country: types.maybeNull(types.string),
    enableSubmit: types.maybeNull(types.boolean),
    grade: types.maybeNull(types.string),
    intialDetails: types.maybeNull(IntialDetails),
    isApiError: types.boolean,
    isCountryListFetched: types.boolean,
    isGradeListFetched: types.boolean,
    isUpdateApiInprogress: types.boolean,
    listOfCountries: types.maybeNull(types.array(Options)),
    listOfGrades: types.maybeNull(types.array(Options)),
    listOfMonths: types.maybeNull(types.array(Options)),
    listOfyears: types.maybeNull(types.array(Options)),
    reasonCode: types.maybeNull(types.string),
    ruleId: types.maybeNull(types.string)
  }
).actions(self => ({
  setCountriesList(data) {
    self.listOfCountries = data;
  },
  setCountry(data) {
    self.country = data;
    self.setEnableSubmit();
  },
  setGradeList(data) {
    self.listOfGrades = data;
  },
  setGrade(data) {
    self.grade = data;
    self.setEnableSubmit();
  },
  setMonthList(data) {
    self.listOfMonths = data;
  },
  setBirthMonth(data) {
    self.birthMonth = data;
    self.setEnableSubmit();
  },
  setYearList(data) {
    self.listOfyears = data;
  },
  setBirthYear(data) {
    self.birthYear = data;
    self.setEnableSubmit();
  },
  setEnableSubmit() {
    if (self.grade === FACULTY_OR_INSTRUCTOR) {
      self.enableSubmit = !!(self.country && self.grade);
    } else {
      const birthYear = self.birthYear;
      const birthMonth = self.birthMonth;
      const currentMonth = new Date().getMonth() + 1;
      const currentYear = new Date().getFullYear();

      if (birthYear && parseInt(birthYear, 10) === currentYear) {
        if (self.country && self.grade && birthMonth && parseInt(birthMonth, 10) < currentMonth) {
          self.enableSubmit = true;
        } else {
          self.enableSubmit = false;
        }
      } else {
        self.enableSubmit = !!(self.country && self.grade && self.birthMonth && self.birthYear);
      }
    }
  },
  setError(data) {
    self.isApiError = data;
  },
  setRuleId(data) {
    self.ruleId = data;
  },
  setIntialValues() {
    self.setCountry(self.intialDetails.userCountry);
    self.setGrade(self.intialDetails.studyLevel);
    if (self.intialDetails.dob) {
      let dateOfBirth = self.intialDetails.dob;
      dateOfBirth = dateOfBirth.split('/');

      self.setBirthMonth(dateOfBirth[0]);
      self.setBirthYear(dateOfBirth[2]);
    } else {
      self.setBirthMonth(null);
      self.setBirthYear(null);
    }
  },
  updateUserDetails() {
    const birthMonth = self.birthMonth;
    const birthYear = self.birthYear;
    const grade = self.grade;
    const country = self.country;
    const lastDayOfMonth = new Date(Number(birthYear), Number(birthMonth), 0);
    const birthDate = `${birthMonth}/${lastDayOfMonth.getDate()}/${birthYear}`;
    const userDetailsPayload = {};
    const intialDetails = self.intialDetails;
    let isValueUpdated = false;
    const { id: userId } = Framework.getStoreRegistry().getStore('user');

    if (grade !== FACULTY_OR_INSTRUCTOR && intialDetails.dob !== birthDate) {
      userDetailsPayload.dob = birthDate;
      isValueUpdated = true;
    }
    if (intialDetails.studyLevel !== grade) {
      userDetailsPayload.studyLevel = grade;
      isValueUpdated = true;
    }
    if (intialDetails.userCountry !== country) {
      userDetailsPayload.country = country;
      isValueUpdated = true;
    }
    if (isValueUpdated) {
      userDetailsPayload.iesUserId = userId;
      self.isUpdateApiInprogress = true;
      Framework.getEventManager().publish(USER_DETAILS_UPDATE_REQUESTED, userDetailsPayload);
    }
  },
  setReasonCode(data) {
    self.reasonCode = data;
  },
  setIsUpdateApiInprogress(data) {
    self.isUpdateApiInprogress = data;
  },
  fetchCountryList() {
    Framework.getEventManager().publish(COUNTRY_LIST_REQUESTED);
    self.isCountryListFetched = false;
  },
  setIsCountryListFetched(data) {
    self.isCountryListFetched = data;
  },
  fetchGradeList() {
    Framework.getEventManager().publish(GRADE_LIST_REQUESTED);
    self.isGradeListFetched = false;
  },
  setIsGradeListFetched(data) {
    self.isGradeListFetched = data;
  },
  setIntialDetails(data) {
    self.intialDetails = data;
  }
}));

export default UserDetails;
