import { types } from 'mobx-state-tree';
import LearningResource from './LearningResource';
/**
 * @author Sarathkumar Venkatachalam
 */

const AssignmentItem = types.model(
  'AssignmentItem',
  {
    id: types.string,
    title: types.string,
    dueDate: types.integer,
    status: types.optional(types.string, ''),
    state: types.maybeNull(types.string),
    showScores: types.maybeNull(types.boolean),
    points: types.maybeNull(types.number),
    percentage: types.maybeNull(types.number),
    pointsEarned: types.maybeNull(types.number),
    lastUpdatedTime: types.integer,
    availabilityDate: types.maybeNull(types.integer),
    children: types.optional(types.array(LearningResource), [])
  }
);
export default AssignmentItem;
