/**
 * Mobx model(s) related to Offer
 *
 * @file Marquee.js
 * @author Poornima N
 */

import { types } from 'mobx-state-tree';
import Media from './Media';

/**
    * A mobx model for Marquee
    *
    */

const Content = types.model(
    'Content',
    {
        media: types.maybeNull(types.compose(Media)),
        text: types.maybeNull(types.string)
    }
)

const Action = types.model(
    'Action',
    {
        text: types.maybeNull(types.string),
        launchURL: types.maybeNull(types.string),
        target: types.maybeNull(types.string)
    }
)
const Slides = types.model(
    'Slides',
    {
        id: types.maybeNull(types.string),
        template: types.maybeNull(types.string),
        title: types.maybeNull(types.string),
        description: types.maybeNull(types.string),
        backgroundImageURL: types.maybeNull(types.string),
        action: types.maybeNull(types.compose(Action)),
        content:  types.maybeNull(types.compose(Content)),
        basePath: types.maybeNull(types.string),
        platform: types.array(types.string)
    }
)
const Marquee = types.model(
    'Marquee',
    {
        displayOrder: types.maybeNull(types.number),
        platform: types.array(types.string),
        slides: types.array(Slides)
    }
);

export default Marquee;
