/**
 * Mobx model(s) related to DisplayOrder
 *
 * @file DisplayOrder.js
 * @author Poornima N
 */
import { types, applySnapshot } from 'mobx-state-tree';

import Display from './Display';
import Marquee from './Marquee';

const DisplayOrder = types.model(
  'DisplayOrder',
  {
    header: types.compose(Display),
    marquee: types.compose(Marquee),
    library: types.compose(Display),
    channel: types.compose(Display),
    offer: types.compose(Display),
    footer: types.compose(Display)
  }
).actions(self => ({
  fetch() {
    Framework.getEventManager().publish(constants.DISPLAY_ORDER_CONTENT_REQUESTED, {});
  },
  set(data) {
    applySnapshot(self, data);
  },
  setError() {
    applySnapshot(self, {});
    self.isError = true;
  }
}));

export default DisplayOrder;




