import React from 'react';
import './loader.scss';
import LoadingCardSkeleton from './LoadingCardSkeleton';
import Footer from '../Footer';
import CommonUtils from '../../../common/utils/CommonUtils';

function LoadingCard() {
  const footerMenuList = CommonUtils.footerMenuList();
  return (
    <div style={{ background: '#05112A', height: '100%', display: 'flex', flexDirection: 'column' }}>

      <div className="loading-marquee-skelton-background" >
        <div className="loading-header">
          <LoadingCardSkeleton skeletonStyle='loading-header-logo' />
          <div className="loading-header-profile-icon">
            <div className="profile1"><LoadingCardSkeleton skeletonStyle='loading-header-profile' /></div>
            <div className="profile2"><LoadingCardSkeleton skeletonStyle='loading-header-profile' /></div>
          </div>
        </div>
        <div style={{ display: 'flex', flexDirection: 'row' }}>
          <div className='loading-marquee-skelton-wrapper'>
            <div style={{ display: 'flex', flexDirection: 'column' }}>
              <LoadingCardSkeleton skeletonStyle='loading-marquee-titletext' />
              <LoadingCardSkeleton skeletonStyle='loading-marquee-description' />
              <LoadingCardSkeleton skeletonStyle='loading-marquee-button' />
            </div>
          </div>
          <div className="loading-marquee-image-wrapper">
            <LoadingCardSkeleton skeletonStyle='loading-marquee-image' />
          </div>
        </div>
      </div>
      <div className="loading-container">
        <div>
          <LoadingCardSkeleton skeletonStyle='loading-library-title' />
        </div >
        <div className="library">
          <div className="library-card-content"><LoadingCardSkeleton skeletonStyle="loading-library-card" /></div>
          <div className="librarycard1"><LoadingCardSkeleton skeletonStyle="loading-library-card" /></div>
          <div className="librarycard2"><LoadingCardSkeleton skeletonStyle="loading-library-card" /></div>
          <div className="librarycard3"><LoadingCardSkeleton skeletonStyle="loading-library-card" /></div>
          <div className="librarycard4"><LoadingCardSkeleton skeletonStyle="loading-library-card" /></div>
        </div>
        <div><LoadingCardSkeleton skeletonStyle="loading-library-button" /></div>
        <div>
          <LoadingCardSkeleton skeletonStyle='loading-channels-title' />
        </div>
        <div className='loading-channels-wrapper'>
          <div className="channelcard1"><LoadingCardSkeleton skeletonStyle="loading-channels-card" /></div>
          <div className="channelcard2"><LoadingCardSkeleton skeletonStyle="loading-channels-card" /></div>
          <div className="channelcard3"><LoadingCardSkeleton skeletonStyle="loading-channels-card" /></div>
          <div className="channelcard4"><LoadingCardSkeleton skeletonStyle="loading-channels-card" /></div>
        </div>
        <div className="loading-offers-container">
          <div>
            <LoadingCardSkeleton skeletonStyle='loading-offers-title' />
          </div>
          <div className='loading-offers-wrapper'>
            <div><LoadingCardSkeleton skeletonStyle="loading-offers-card" /></div>
            <div><LoadingCardSkeleton skeletonStyle="loading-offers-card" /></div>
            <div><LoadingCardSkeleton skeletonStyle="loading-offers-card" /></div>
            <div><LoadingCardSkeleton skeletonStyle="loading-offers-card" /></div>
          </div>
        </div>
      </div>
      <Footer {...footerMenuList} />
    </div >
  )
}

export default LoadingCard;