/**
 * Styled Autocomplete
 *
 * @file StyledAutocomplete.jsx
 * @author Srinivasan Bakthavatchalam
 */

import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import List from '@material-ui/core/List';

import CustomScrollContainer from './CustomScrollContainer';

const styles = () => ({
  colorWhite: {
    color: '#F7F9FD !important'
  },
  listbox: {
    maxHeight: 'unset',
    overflow: 'hidden',
    padding: 0
  },
  optionsStyles: {
    backgroundColor: '#182849 !important',
    borderBottom: '1px solid #4F5561',
    color: '#F7F9FD !important',
    cursor: 'pointer',
    fontFamily: 'Hind',
    fontStyle: 'normal',
    fontWeight: 400,
    fontSize: 16,
    lineHeight: '150%',
    '&:hover': {
      backgroundColor: '#1D3058 !important',
      borderColor: 'transparent',
      fontWeight: 700
    },
    '&[aria-selected="true"]': {
      backgroundColor: '#1D3058 !important',
      borderColor: 'transparent',
      fontWeight: 700
    }
  },
  selectInput: {
    color: '#F7F9FD !important',
    fontFamily: 'Hind',
    fontStyle: 'normal',
    fontWeight: 400,
    fontSize: 16,
    lineHeight: '150%',
    height: 26,
    padding: '0px !important'
  },
  selectPaper: {
    backgroundColor: '#182849 !important',
    border: '1px solid #E8EEFA',
    borderBottomLeftRadius: 4,
    borderBottomRightRadius: 4,
    borderRadius: 'unset',
    color: '#F7F9FD !important',
    margin: 0,
    marginRight: -1,
    marginTop: -2,
    minHeight: 'unset'
  },
  textFieldStyles: {
    background: '#182849',
    border: '1px solid #E8EEFA',
    borderRadius: 4,
    color: '#F7F9FD !important',
    height: 44,
    marginBottom: 40,
    '& .MuiOutlinedInput-root': {
      '&.Mui-focused fieldset': {
        border: '2px solid #FEFEFE',
        borderRadius: 6,
        margin: -6
      }
    }
  }
});

const StyledAutocomplete = ({
  classes,
  disabled,
  id,
  options,
  value,
  onChange,
  placeholder
}) => (
  <Autocomplete
    autoHighlight
    disabled={disabled}
    disableClearable
    classes={{
      clearIndicator: classes.colorWhite,
      input: classes.selectInput,
      listbox: classes.listbox,
      option: classes.optionsStyles,
      paper: classes.selectPaper,
      popupIndicator: classes.colorWhite,
      root: classes.textFieldStyles
    }}
    fullWidth
    id={id}
    ListboxComponent={params => (
      <CustomScrollContainer>
        <List {...params} />
      </CustomScrollContainer>
    )}
    options={options.map(option => option.label)}
    value={value}
    onChange={(event, newValue) => {
      const selectedOption = options.find(option => option.label === newValue);

      onChange(event, selectedOption.value);
    }}
    renderInput={params => (
      <TextField
        {...params}
        inputProps={{
          ...params.inputProps,
          autoComplete: 'off' // disable autocomplete and autofill
        }}
        placeholder={placeholder}
        variant="outlined"
      />
    )}
  />
);

StyledAutocomplete.propTypes = {
  classes: PropTypes.object.isRequired,
  disabled: PropTypes.bool,
  id: PropTypes.string,
  options: PropTypes.array,
  onChange: PropTypes.func,
  placeholder: PropTypes.string,
  value: PropTypes.string
};

StyledAutocomplete.defaultProps = {
  disabled: false,
  options: [],
  id: 'or-missing-details',
  onChange: () => {},
  placeholder: '',
  value: ''
};

export default withStyles(styles)(StyledAutocomplete);
