/**
 * Index file for all environment setting files.
 *
 * @author Hari Gangadharan
 */
import base from './base';
import local from './local';
import dev from './dev';
import qa from './qa';
import qaInt from './qaInt';
import stg from './stg';
import perf from './perf';
import prod from './prod';
import saet from './flavors/saet';
import mojoStg from './mojoStg';
import mojoProd from './mojoProd';
import etextPerf from './etextPerf';

let env;
let productFlavor;

switch (window.location.hostname) {
  case 'localhost':
  case 'local-dev.pearson.com':
    env = stg;
    productFlavor = saet;
    break;
  case 'ise-dev.dev-prsn.com':
  case 'ise-dev.pearson.com':
  case 'etext-ise-dev.pearson.com':
    env = dev;
    productFlavor = saet;
    break;
  case 'etext-ise-qa.pearson.com':
    env = stg;
    productFlavor = saet;
    break;
  case 'etext-ise-qa-int.pearson.com':
    env = qaInt;
    productFlavor = saet;
    break;
  case 'etext-ise-stg.pearson.com':
    env = stg;
    productFlavor = saet;
    break;
  case 'plus-stg.pearson.com':
  case 'plus-qa.pearson.com':
    env = mojoStg;
    productFlavor = saet;
    break;
  case 'etext-ise-perf.pearson.com':
    env = etextPerf;
    productFlavor = saet;
    break;
  case 'plus-ppe.pearson.com':
    env = perf;
    productFlavor = saet;
    break;
  // Production
  case 'etext-ise.pearson.com':
    env = prod;
    productFlavor = saet;
    break;
  case 'plus.pearson.com':
    env = mojoProd;
    productFlavor = saet;
    break;
  default:
    env = stg;
    productFlavor = saet;
}

/**
 * Override the base values with the environment specific values.
 */
export default { ...base, ...productFlavor, ...env };
