/**
 * Service related to CountryList
 *
 * @file CountryList.js
 * @author Srinivasan Bakthavatchalam
 */

import Framework, { BaseService } from '@greenville/framework';

import {
  GRADE_LIST_REQUESTED
} from '../../../common/constants';
import UserDetailsUtils from '../../../common/utils/UserDetailsUtils';

export default class GradeList extends BaseService {
  constructor() {
    super(GRADE_LIST_REQUESTED, 'gradeListStatus');
  }

  handleEvent() {
    return this.getClient('marin').get('/static/studylevels');
  }

  handleResponse(response) { // eslint-disable-line class-methods-use-this
    const { data } = response;

    if (data) {
      const gradeLabelValuePair = UserDetailsUtils.getLabelValuePair(data, 'description', 'studyLevel');
      const userGrade = Framework.getStoreRegistry().getStore('userdetails').intialDetails.studyLevel;

      Framework.getStoreRegistry().getStore('userdetails').setGradeList(gradeLabelValuePair);
      Framework.getStoreRegistry().getStore('userdetails').setGrade(userGrade);
      Framework.getStoreRegistry().getStore('userdetails').setIsGradeListFetched(true);
    }
  }

  handleError() { // eslint-disable-line class-methods-use-this
    Framework.getStoreRegistry().getStore('userdetails').setIsGradeListFetched(false);
    Framework.getStoreRegistry().getStore('userdetails').setError(true);
    Framework.getStoreRegistry().getStore('userdetails').setReasonCode('R99');
    Framework.getStoreRegistry().getStore('userdetails').setGradeList([]);
  }
}
