/**
 * Service related to CountryList
 *
 * @file CountryList.js
 * @author Srinivasan Bakthavatchalam
 */

import Framework, { BaseService } from '@greenville/framework';

import {
  COUNTRY_LIST_REQUESTED
} from '../../../common/constants';
import UserDetailsUtils from '../../../common/utils/UserDetailsUtils';

export default class CountryList extends BaseService {
  constructor() {
    super(COUNTRY_LIST_REQUESTED, 'countryListStatus');
  }

  handleEvent() {
    return this.getClient('marin').get('/countries');
  }

  handleResponse(response) { // eslint-disable-line class-methods-use-this
    const { data } = response.data;

    if (data && data.createAccountCountries) {
      const countryLabelValuePair = UserDetailsUtils.getLabelValuePair(data.createAccountCountries, 'text', 'value');
      const userCountry = Framework.getStoreRegistry().getStore('userdetails').intialDetails.userCountry;

      Framework.getStoreRegistry().getStore('userdetails').setCountriesList(countryLabelValuePair);
      Framework.getStoreRegistry().getStore('userdetails').setCountry(userCountry);
      Framework.getStoreRegistry().getStore('userdetails').setIsCountryListFetched(true);
    }
  }

  handleError() { // eslint-disable-line class-methods-use-this
    Framework.getStoreRegistry().getStore('userdetails').setIsCountryListFetched(false);
    Framework.getStoreRegistry().getStore('userdetails').setError(true);
    Framework.getStoreRegistry().getStore('userdetails').setReasonCode('R99');
    Framework.getStoreRegistry().getStore('userdetails').setCountriesList([]);
  }
}
