import { BaseService } from '@greenville/framework';
import * as constants from '../constants';
import env from '../env';

/**
* @Author Mohamed yasar arabath M
*/

export default class AppcuesService extends BaseService {
  constructor() {
    super(constants.APPCUES_USER_PROFILE);
    this.callBack = null;
  }

  handleEvent(eventName, { userId, callBack }) {
    const appcuesFetchUser = `/accounts/${env.APPCUES_ACCOUNT_ID}/users/${userId}/profile`;
    const headerParams = {
      headers: {
        Authorization: `${env.APPCUES_AUTH}`
      }
    };
    this.callBack = callBack;
    return this.getClient('appcues').get(appcuesFetchUser, headerParams);
  }

  handleResponse(response) {
    if (this.callBack) {
      this.callBack(response.data);
    }
  }

  handleError(error) {
    if (this.callBack) {
      this.callBack(null);
    }
    console.log(error, 'appcues error');
  }
}
