/**
 * Mobx model(s) related to Offer
 *
 * @file Offer.js
 * @author Mohamed yasar arabath M
 */

import { types } from 'mobx-state-tree';
import OfferDetail from './OfferDetail';
/**
    * A mobx model for Offer
    *
    */
const Offer = types.model(
  'Offer',
  {
    base_path: types.maybeNull(types.string),
    description: types.maybeNull(types.string),
    design_id: types.maybeNull(types.string),
    id: types.maybeNull(types.string),
    offer_detail: types.maybeNull(OfferDetail),
    offer_image: types.maybeNull(types.string),
    redemption_link: types.maybeNull(types.string),
    sort_order: types.maybeNull(types.number),
    subtitle: types.maybeNull(types.string),
    thumbnail_image: types.maybeNull(types.string),
    title: types.maybeNull(types.string)
  }
);

export default Offer;
