import { types, applySnapshot } from 'mobx-state-tree';
import Framework from '@greenville/framework';
import * as constants from '../../../common/constants';
import AssignmentItem from './AssignmentItem';

/**
* Author Sarathkumar Venkatachalam
*/

const Assignments = types.model(
  'Assignments',
  {
    items: types.array(AssignmentItem)
  }
).actions(self => ({

  fetch(id) {
    if (self.id === id) {
      Framework.getEventManager().publish(constants.ASSIGNMENT_FETCHED, {});
    } else {
      Framework.getEventManager().publish(constants.ASSIGNMENT_REQUESTED, { courseId: id });
    }
  },

  set(assignments) {
    applySnapshot(self, { items: assignments });
    Framework.getEventManager().publish(constants.ASSIGNMENT_FETCHED, {});
  },

  setAssignmentsAsEmpty() {
    applySnapshot(self, { items: [] });
    Framework.getEventManager().publish(constants.ASSIGNMENT_FETCHED, {});
  }
}));
export default Assignments;
