import { types, applySnapshot } from 'mobx-state-tree';
/**
 * @author Arish Kumar K
 */

const CourseContainerState = types.model(
  'CourseContainerState',
  {
    showConfigToc: types.optional(types.boolean, false),
    showSuccessMsg: types.optional(types.boolean, false)
  }
).actions(self => ({
  set(state) {
    applySnapshot(self, state);
  }
}));
export default CourseContainerState;
