import { types } from 'mobx-state-tree';
/**
   * A mobx model for OfferEligiblity
   *
   */
const OfferEligiblity = types.model(
  'OfferEligiblity',
  {
    type: types.maybeNull(types.string),
    value: types.maybeNull(types.boolean)
  }
);

export default OfferEligiblity;
