import Framework, { BaseService } from '@greenville/framework';

import {
  DISPLAY_ORDER_CONTENT_REQUESTED,
  DISPLAY_ORDER_CONTENT_SUCCESS
} from '../../../common/constants';

/**
* @Author Mohamed yasar arabath M
*/

export default class DisplayOrderService extends BaseService {
  constructor() {
    super(DISPLAY_ORDER_CONTENT_REQUESTED, 'dispalyordestatus');
    this.error = false;
  }

  handleEvent(eventName, event) {
    const { locale, flag } = event;
    const getPersonalisedTemplate = flag ? `&version=1.0.0` : '';

    return this.getClient('marin').get(`/templates/home?locale=${locale}${getPersonalisedTemplate}`);
  }

  handleResponse(response) {
    this.error = false;
    const { data } = response;
    if (data) {
      Framework.getEventManager().publish(DISPLAY_ORDER_CONTENT_SUCCESS, data);
      Framework.getStoreRegistry().getStore('displayorder').set(data);
    }
  }

  handleError(error) {
    this.error = true;
    Framework.getEventManager().publish(DISPLAY_ORDER_CONTENT_SUCCESS, {});
    Framework.getStoreRegistry().getStore('displayorder').setError();
    console.log(error, 'displayorder Content service error');
  }
}
