/**
 * User Detail Form
 *
 * @file UserDetailForm.jsx
 * @author Srinivasan Bakthavatchalam
 */
import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Info from '@material-ui/icons/InfoOutlined';
import { IconButton } from '@material-ui/core';

import StyledAutocomplete from '../StyledAutocomplete';
import StyledLabel from '../StyledLabel';
import StyledHeader from '../StyleHeader';
import UserDetailFormStyles from './UserDetailFormStyles';
import LabelTooltip from '../LabelTooltip';
import { FACULTY_OR_INSTRUCTOR } from '../../../../common/constants';

const UserDetailForm = ({
  classes,
  country,
  countryList,
  disabled,
  grade,
  gradeList,
  isInstructor,
  language,
  month,
  months,
  setBirthMonth,
  setBirthYear,
  setCountry,
  setGrade,
  year,
  years
}) => {
  /**
   * Handles country change
   * @param {*} event
   * @param {*} newValue
   */
  const handleCountryChange = (event, newValue) => {
    setCountry(newValue);
  };
  /**
   * Handles study level change
   * @param {*} event
   * @param {*} newValue
   */
  const handleGradeLevelChange = (event, newValue) => {
    setGrade(newValue);
  };
  /**
   * Handles bith month change
   * @param {*} event
   * @param {*} newValue
   */
  const handleMonthChange = (event, newValue) => {
    setBirthMonth(newValue);
  };
  /**
   * Handles birth year change
   * @param {*} event
   * @param {*} newValue
   */
  const handleYearChange = (event, newValue) => {
    setBirthYear(newValue);
  };

  useEffect(() => {
    // if isInstructor and grade is empty
    // then update grade as Factulty or instructor
    if (isInstructor && !grade) {
      setGrade(FACULTY_OR_INSTRUCTOR);
    }
  }, [isInstructor]);

  return (
    <>
      <StyledHeader
        headingText={language.getMessage('offers.POPUP_HEADING')}
        subHeadingText={language.getMessage('offers.POPUP_SUBHEADING')}
      />
      <div className={classes.dataContainer}>
        <div>
          <StyledLabel
            lable={language.getMessage('offers.COUNTRY')}
          />
          <StyledAutocomplete
            disabled={disabled}
            id="or-missing-detials-country"
            options={countryList}
            value={countryList.find(option => option.value === country)?.label}
            onChange={handleCountryChange}
            placeholder={language.getMessage('offers.COUNTRY_SELECT')}
          />
        </div>
        <div>
          <div className={classes.labelContainer}>
            <StyledLabel
              lable={language.getMessage('offers.ROLE')}
            />
            <LabelTooltip
              title={language.getMessage('offers.ROLE_TOOLTIP')}
              placement="right"
            >
              <IconButton className={classes.iconButton}>
                <Info />
              </IconButton>
            </LabelTooltip>
          </div>
          <StyledAutocomplete
            disabled={disabled}
            id="or-missing-detials-role"
            options={gradeList}
            value={gradeList.find(option => option.value === grade)?.label}
            onChange={handleGradeLevelChange}
            placeholder={language.getMessage('offers.ROLE_SELECT')}
          />
        </div>
        { grade && grade !== FACULTY_OR_INSTRUCTOR && (
        <div>
          <div className={classes.labelContainer}>
            <StyledLabel
              lable={language.getMessage('offers.MONTH_YEAR')}
            />
            <LabelTooltip
              title={language.getMessage('offers.DOB_TOOLTIP')}
              placement="right"
            >
              <IconButton className={classes.iconButton}>
                <Info />
              </IconButton>
            </LabelTooltip>
          </div>
          <div className={classes.birthDateContainer}>
            <div className={classes.birthDataItem}>
              <StyledAutocomplete
                disabled={disabled}
                id="or-missing-detials-month"
                options={months}
                value={months.find(option => option.value === month)?.label}
                onChange={handleMonthChange}
                placeholder={language.getMessage('offers.MONTH_SELECT')}
              />
            </div>
            <div className={classes.birthDataItem}>
              <StyledAutocomplete
                disabled={disabled}
                id="or-missing-detials-year"
                options={years}
                value={years.find(option => option.value === year)?.label}
                onChange={handleYearChange}
                placeholder={language.getMessage('offers.YEAR_SELECT')}
              />
            </div>
          </div>
        </div>
        )}
      </div>
    </>
  );
};

UserDetailForm.propTypes = {
  classes: PropTypes.object.isRequired,
  country: PropTypes.string,
  countryList: PropTypes.array,
  disabled: PropTypes.bool,
  grade: PropTypes.string,
  gradeList: PropTypes.array,
  isInstructor: PropTypes.bool,
  language: PropTypes.object.isRequired,
  month: PropTypes.string,
  months: PropTypes.array,
  setBirthMonth: PropTypes.func,
  setBirthYear: PropTypes.func,
  setCountry: PropTypes.func,
  setGrade: PropTypes.func,
  year: PropTypes.string,
  years: PropTypes.array
};

UserDetailForm.defaultProps = {
  country: '',
  countryList: [],
  disabled: false,
  grade: '',
  gradeList: [],
  isInstructor: false,
  month: '',
  months: [],
  setBirthMonth: () => {},
  setBirthYear: () => {},
  setCountry: () => {},
  setGrade: () => {},
  year: '',
  years: []
};

export default withStyles(UserDetailFormStyles)(UserDetailForm);
