import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import { FormattedMessage } from 'react-intl';
import { Icon } from '@greenville/mui-theme';

/**
 * A Dialog component to show success of toc contents publish.
 *
 *
 */

const styles = () => ({
  contentRoot: {
    paddingLeft: 50
  },
  removeRoot: {
    position: 'absolute',
    top: 3,
    right: 5
  },
  checkIconRoot: {
    position: 'absolute',
    top: 35,
    left: 25
  }
});

const SuccessDialog = ({
  showSuccessMsg, onClose, language, classes
}) => (
  <Dialog
    open={showSuccessMsg}
    onClose={onClose}
    id="success-dialog"
  >
    <DialogContent classes={{ root: classes.contentRoot }}>
      <IconButton onClick={onClose} classes={{ root: classes.removeRoot }}>
        <Icon name="Remove" color="action" fontSize="small" />
      </IconButton>
      <Icon name="CheckCircleFilled" color="primary" classes={{ root: classes.checkIconRoot }} />
      <Typography variant="h4">
        <FormattedMessage {...language.getText('courses.toc.PUBLISH_SUCCESS')} />
      </Typography>
      <Typography variant="body2">
        <FormattedMessage {...language.getText('courses.toc.PUBLISH_MESSAGE')} />
      </Typography>
    </DialogContent>
  </Dialog>
);


SuccessDialog.propTypes = {
  showSuccessMsg: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
  language: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(SuccessDialog);
