/**
 * Offer Details Conta iner
 *
 * @file OfferDetailsContainer.jsx
 * @author Srinivasan Bakthavatchalam
 */

import React, { useEffect, useState, Fragment } from 'react';
import PropTypes from 'prop-types';
import Framework, { shapes } from '@greenville/framework';
import { getSnapshot } from 'mobx-state-tree';
import { inject } from 'mobx-react';
import { withLDConsumer } from 'launchdarkly-react-client-sdk';
import { CircularProgress, Typography, withStyles } from '@material-ui/core';
import { Offers, OfferDetails } from '@onereader/offers';

import * as constants from '../../common/constants';
import Course from '../courses/model/Course';
import Product from '../products/model/Product';
import * as OffersModel from '../offers/model/Offers';
import OffersPlugin from '../offers';
import CommonUtils from '../../common/utils/CommonUtils';
import OffersUtils from '../../common/utils/OffersUtils';

const styles = () => ({
  moreOffers: {
    color: '#FEFEFE',
    fontFamily: 'TT Commons',
    fontSize: 30,
    fontStyle: 'normal',
    fontWeight: 700,
    lineHeight: '110%',
    marginBottom: 24
  },
  moreOffersWrapper: {
    padding: '50px 75px 75px 75px'
  },
  offerDetails: {
    margin: '43px 75px 0px 75px',
    paddingBottom: 50
  },
  offerDetailsContainer: {
    background: '#05112A',
    position: 'absolute',
    zIndex: -1
  },
  offerDetailsWrapper: {
    background: '#05112A',
    height: 'auto',
    overflow: 'hidden',
    position: 'relative',
    width: '100%',
    zIndex: 1,
    '&:before': {
      top: -427,
      left: -158,
      content: '""',
      position: 'absolute',
      background: 'linear-gradient(0.25deg, rgba(255, 255, 255, 0) 9.71%, #FFFFFF 47.46%, #FFFFFF 62.57%, rgba(255, 255, 255, 0) 100.32%)',
      mixBlendMode: 'overlay',
      filter: 'opacity(0.325)',
      transform: 'rotate(-34.35deg)',
      width: 659,
      height: 671,
      zIndex: -1
    },
    '&:after': {
      bottom: -194,
      right: -42,
      content: '""',
      position: 'absolute',
      background: 'linear-gradient(0.25deg, rgba(255, 255, 255, 0) 9.71%, #FFFFFF 47.46%, #FFFFFF 62.57%, rgba(255, 255, 255, 0) 100.32%)',
      mixBlendMode: 'overlay',
      filter: 'opacity(0.325)',
      transform: 'rotate(-34.35deg)',
      width: 659,
      height: 671,
      zIndex: -1,
      '@media screen and (max-width: 1024px)': {
        right: -493,
        bottom: -80
      }
    }
  }
});

const OfferDetailsContainer = ({
  classes,
  course,
  match,
  language,
  offers,
  user,
  product,
  ldClient
}) => {
  const {
    params: {
      bundleId,
      container,
      id,
      offerId,
      productId
    }
  } = match;
  const { LOCATION } = constants.OFFER_EVENT;
  const selectedOfferId = OffersUtils.getOfferIdFormURL(offerId);
  const [moreAvailableOffers, setMoreAvailableOffers] = useState([]);
  const [selectedOffer, setSelectedOffer] = useState({});
  const [showRedeem, setShowRedeem] = useState(false);

  const offersPlugin = new OffersPlugin({ props: { offers } });
  const handleOfferClick = (cardId, initalLoad = false) => {
    const availableOffers = OffersUtils.getMoreAvailableOffers(getSnapshot(offers.offers_list), cardId);
    const selectedOfferDetails = OffersUtils.getSelectedOfferDetails(getSnapshot(offers.offers_list), cardId);

    setMoreAvailableOffers(availableOffers);
    setSelectedOffer(selectedOfferDetails);
    if (!initalLoad) {
      offersPlugin.offerActionGAEvent(cardId, LOCATION.OFFER_DETAILS);
    }
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const updateLaunchDarklyUser = () => {
    if (user.id && user.ldUserHash) {
      ldClient.identify({
        key: user.id, options: { hash: user.ldUserHash }
      }, user.ldUserHash).then((flags) => {
        offersPlugin.setLDFetched(flags);
      }).catch((err) => {
        offersPlugin.LDFail(err);
      });
    }
  };

  useEffect(() => {
    // Initializing GTM
    CommonUtils.initializeGTM();
    Framework.getEventManager().on(constants.OFFERS_CONTENT_SUCCESS, () => {
      const hasRedeem = OffersUtils.hasRedeem(getSnapshot(offers.eligiblity));
      setShowRedeem(hasRedeem);
      handleOfferClick(selectedOfferId, true);
    });
    offersPlugin.init(constants.EREADER);
    Framework.getEventManager().on(constants.USER_PROFILE_FETCHED, () => {
      updateLaunchDarklyUser();
    });
    if (container === 'courses') {
      Framework.getEventManager().on(constants.COURSE_DATA_FETCHED, () => {
        offersPlugin.setUserRoleFetched(getSnapshot(course));
      });
      course.fetch(id, productId, bundleId);
    } else if (container === 'products') {
      Framework.getEventManager().on(constants.PRODUCT_DATA_FETCHED, () => {
        offersPlugin.setUserRoleFetched(getSnapshot(product));
      });
      product.fetch(id, bundleId);
    }
  }, []);

  return (
    <Fragment>
      { (!offers.isError && !offers.isOffersFetched)
        ? <CircularProgress className="circleStyle" />
        : (
          <div className={classes.offerDetailsContainer}>
            <div className={classes.offerDetailsWrapper}>
              <div className={classes.offerDetails}>
                <div className={classes.offerDetailsBefore} />
                <OfferDetails
                  descriptionHTML={selectedOffer.descriptionHTML}
                  id={selectedOffer.id}
                  onRedeemClick={offersPlugin.handleRedeemClick}
                  locale={language.currentLanguage}
                  showRedeem={showRedeem}
                  redeemLink={selectedOffer.redeemLink}
                  subTitle={selectedOffer.subTitle}
                  title={selectedOffer.title}
                  thumbnailImage={selectedOffer.thumbnailImage}
                  titleImage={selectedOffer.titleImage}
                />
                <div className={classes.offerDetailsAfter} />
              </div>
            </div>
            <div className={classes.moreOffersWrapper}>
              <Typography component="div" className={classes.moreOffers}>
                {language.getMessage('offers.moreOffers')}
              </Typography>
              <Offers
                columns={{
                  xs: 1, sm: 1, md: 2, lg: 3, xl: 3, xxl: 4
                }}
                handleOfferClick={handleOfferClick}
                locale={language.currentLanguage}
                offers={moreAvailableOffers}
              />
            </div>
          </div>
        )
  }
    </Fragment>
  );
};

OfferDetailsContainer.defaultProps = {
  course: {},
  product: {}
};

OfferDetailsContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  course: shapes.modelOf(Course),
  language: PropTypes.object.isRequired,
  match: shapes.match.isRequired,
  offers: shapes.modelOf(OffersModel).isRequired,
  user: shapes.user.isRequired,
  product: shapes.modelOf(Product),
  ldClient: PropTypes.object.isRequired
};

export default withLDConsumer()(inject('course', 'language', 'offers', 'user', 'product')(withStyles(styles)(OfferDetailsContainer)));
