/**
 * ChannelCarouselContainer for ChannelCarousel comp
 *
 * @file ChannelCarouselContainer.jsx
 * @author Vignesh Selvam
 */

import React, { Fragment, memo } from 'react';
import PropTypes from 'prop-types';
import { Box, withStyles } from '@material-ui/core';
import { Grid } from '@study/components';
import '@study/components/dist/index.css';

const ChannelStyle = () => ({
  channelContainer: {
    padding: '26px 64px 22px 64px',
    '@media screen and (max-width: 499px)': {
      padding: '26px 16px 22px 16px'
    },
    '@media screen and (min-width:500px) and (max-width: 1439px)': {
      padding: '26px 32px 22px 32px'
    }
  }
});

const ChannelCarouselContainer = ({
  containerOrder, userDetail, userType, env, locale, classes, numberOfRows, onSeeAllClick
}) => {
  const { id: userId, token } = userDetail;
  return (
    <Fragment>
      <Box className={classes.channelContainer} id="channel-etext" data-title="Channels">
        <Grid
          containerOrder={containerOrder}
          getUserCallback={() => ({ userId, token })}
          userType={userType}
          env={env}
          locale={locale}
          numberOfRows={numberOfRows}
          onSeeAllClick={onSeeAllClick}
        />
      </Box>
    </Fragment>
  );
};
ChannelCarouselContainer.propTypes = {
  containerOrder: PropTypes.number.isRequired,
  userDetail: PropTypes.object.isRequired,
  userType: PropTypes.string.isRequired,
  env: PropTypes.oneOf(['dev', 'qa', 'stg', 'stage', 'perf', 'ppe', 'prod']).isRequired,
  locale: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired,
  numberOfRows: PropTypes.number.isRequired
};
export default memo(withStyles(ChannelStyle)(ChannelCarouselContainer));
