/**
 * Offers Utils
 *
 * @file OffersUtils.js
 * @author Srinivasan Bakthavatchalam
 */


import { Buffer } from 'buffer';
import hmacSHA256 from 'crypto-js/hmac-sha256';
import Base64 from 'crypto-js/enc-base64';
import Framework from '@greenville/framework';
import * as constants from '../constants';
import env from '../env';

export default class OffersUtils {
/**
 * Get more available offer
 *
 * @param {*} OffersData
 * @param {*} selectedOfferId
 * @returns
 */
static getMoreAvailableOffers = (OffersData, selectedOfferId) => {
  const moreAvailableOffers = OffersData.filter(offer => offer.id !== selectedOfferId);

  return moreAvailableOffers;
};

/**
 * Constructs the offer id that need to be placed in url
 *
 * @param {*} id
 * @returns
 */
static getOfferIdForURL = id => id.replace('/', '~:~')

/**
 * Constructs the offer id from the URL
 *
 * @param {*} id
 * @returns
 */
static getOfferIdFormURL = id => id.replace('~:~', '/')

/**
 * Get selected offer
 *
 * @param {*} OffersData
 * @param {*} selectedOfferId
 * @returns
 */
 static getSelectedOffer = (OffersData, selectedOfferId) => {
   const selectedOffer = OffersData.filter(offer => offer.id === selectedOfferId);

   return selectedOffer.length !== 0 ? selectedOffer.pop() : null;
 };

 /**
 * Override smartthinking url
 * @param {*} existingUrl
 * @returns
 */
  static overrideSmartThinkingUrl = (existingUrl) => {
    const currentDataObj = new Date();
    const {
      firstName, lastName, email, gpsSubscriptions
    } = Framework.getStoreRegistry().getStore('user');
    const gpsSubscriptionArray = gpsSubscriptions.toJSON();
    const pearsonSubscriptionId = gpsSubscriptionArray && gpsSubscriptionArray.length ? gpsSubscriptionArray.find(
      subscription => subscription.status === 'ACTIVE' || subscription.status === 'PAUSED'
    ).subscriptionId : 'notpearsonplus';
    const { secret, baseUrl } = env.SMARTTHINKING_CONFIG;
    const reqDetail = {
      firstName,
      lastName,
      email,
      couponCode: 'MOJO@40',
      pearsonSubscriptionId,
      reqTimestamp: currentDataObj.toISOString()
    };
    const reqSignature = Base64.stringify(hmacSHA256(JSON.stringify(reqDetail), secret));
    const data = {
      reqDetails: reqDetail,
      signature: reqSignature
    };
    const signature = Buffer.from(JSON.stringify(data)).toString('base64');
    const redeemLink = `${baseUrl}?data=${signature}`;
    if (redeemLink !== undefined && redeemLink !== null) {
      return redeemLink;
    }
    return existingUrl;
  };

/**
 * Get selected offer details
 *
 * @param {*} OffersData
 * @param {*} selectedOfferId
 * @returns
 */
static getSelectedOfferDetails = (OffersData, selectedOfferId) => {
  let selectedOfferDetails = {};
  const selectedOffer = this.getSelectedOffer(OffersData, selectedOfferId);
  let redeemLink = selectedOffer.redemption_link;
  if (selectedOffer) {
    if (selectedOffer.title === constants.SMART_THINKING) {
      redeemLink = this.overrideSmartThinkingUrl(redeemLink);
    }
    selectedOfferDetails = {
      descriptionHTML: selectedOffer.offer_detail.description,
      id: selectedOfferId,
      redeemLink,
      subTitle: selectedOffer.offer_detail.subtitle,
      thumbnailImage: `${selectedOffer.base_path}${selectedOffer.offer_detail.detail_image}`,
      titleImage: `${selectedOffer.base_path}${selectedOffer.offer_detail.thumbnail_image}`,
      title: selectedOffer.offer_detail.title
    };
  }

  return selectedOfferDetails;
};

/**
 * Handles offer click from drawer
 *
 * @param {*} cardId
 * @param {*} url
 * @param {*} currentRoute
 * @param {*} routeId
 * @param {*} productId
 * @param {*} bundleId
 */
static handleOffersRedirection = (cardId, url, currentRoute, routeId, productId = null, bundleId = null) => {
  let urlPrefix = url;

  if (urlPrefix.indexOf(currentRoute) > -1) {
    urlPrefix = url.split(currentRoute)[0];
  }
  urlPrefix = `${urlPrefix}offer/${currentRoute}/${routeId}`;
  if (productId) {
    urlPrefix = `${urlPrefix}/products/${productId}`;
  }
  if (bundleId) {
    urlPrefix = `${urlPrefix}/bundles/${bundleId}`;
  }
  window.open(`${urlPrefix}/offerDetails/${this.getOfferIdForURL(cardId)}`, '_blank');
}

/**
 * Handles Offers show/hide based Lanuch Darkly and user role
 *
 * @param {*} currentPage
 * @param {*} ldflags
 * @param {*} userRole
 */
 static showOffers = (currentPage, LDflags, isUserStudent) => {
   const {
     showOffersForStudentInHome, showOffersForInstructorInHome,
     showOffersForStudentIneReader, showOffersForInstructorIneReader
   } = LDflags;
   const offerShownCases = [{
     page: constants.AUTH_HOME,
     ldFlag: showOffersForStudentInHome,
     isStudent: true
   },
   {
     page: constants.AUTH_HOME,
     ldFlag: showOffersForInstructorInHome,
     isStudent: false
   },
   {
     page: constants.EREADER,
     ldFlag: showOffersForStudentIneReader,
     isStudent: true
   },
   {
     page: constants.EREADER,
     ldFlag: showOffersForInstructorIneReader,
     isStudent: false
   }
   ];
   return offerShownCases.some(
     ({ page, ldFlag, isStudent }) => (
       (page === currentPage)
       && ldFlag
       && (isStudent === isUserStudent)
     )
   );
 }

 /**
 * Handles Offers show/hide based Lanuch Darkly and user role
 *
 * @param {array} eligiblity
 *
 */
  static hasRedeem = eligiblity => eligiblity.some(({ type, value }) => (type === 'REDEMPTION' && value))
}
