/**
 * Service related to UserDetails
 *
 * @file UserDetails.js
 * @author Srinivasan Bakthavatchalam
 */

import Framework, { BaseService } from '@greenville/framework';

import {
  OFFERS_CONTENT_NOT_ELIGIBLE,
  USER_DETAILS_UPDATE_REQUESTED
} from '../../../common/constants';

export default class UserDetails extends BaseService {
  constructor() {
    super(USER_DETAILS_UPDATE_REQUESTED, 'userDetailsStatus');
  }

  handleEvent(eventName, userDetails) {
    return this.getClient('marin').put('/userprofile', userDetails);
  }

  handleResponse(response) { // eslint-disable-line class-methods-use-this
    const { data } = response;
    if (data === 'success') {
      Framework.getStoreRegistry().getStore('offers')
        .fetchOffersEligibility().then((hasEligiblity) => {
          if (hasEligiblity) {
            Framework.getStoreRegistry().getStore('offers').fetchOffers();
          } else {
            Framework.getEventManager().publish(OFFERS_CONTENT_NOT_ELIGIBLE);
            Framework.getStoreRegistry().getStore('offers').setOffers([]);
          }
          Framework.getStoreRegistry().getStore('userdetails').setIsUpdateApiInprogress(false);
        });
    }
  }

  handleError() { // eslint-disable-line class-methods-use-this
    Framework.getStoreRegistry().getStore('userdetails').setError(true);
    Framework.getStoreRegistry().getStore('userdetails').setReasonCode('R99');
    Framework.getStoreRegistry().getStore('userdetails').setIsUpdateApiInprogress(false);
  }
}
