/**
 * Mobx model(s) related to offers
 *
 * @file Offers.js
 * @author Mohamed yasar arabath M
 */

import { types } from 'mobx-state-tree';
import Framework from '@greenville/framework';

import { OFFERS_CONTENT_REQUESTED, OFFERS_ELIGIBILITY_REQUESTED } from '../../../common/constants';
import OfferEligiblity from './OfferEligiblity';
import Offer from './Offer';

/**
  * A mobx model for Offers
  *
  */
const Offersm = types.model(
  'Offers',
  {
    eligiblity: types.array(OfferEligiblity),
    offers_list: types.array(Offer),
    isEligiblityFetched: types.optional(types.boolean, false),
    isOffersFetched: types.optional(types.boolean, false),
    isError: types.optional(types.boolean, false),
    showUserDetails: types.optional(types.boolean, false),
    reasonCode: types.optional(types.string, '')
  }
).actions(self => ({
  fetchOffersEligibility(showOffersUserDetailModal = false) {
    const { id: userId } = Framework.getStoreRegistry().getStore('user');
    return new Promise((resolveCallBack) => {
      Framework.getEventManager().publish(OFFERS_ELIGIBILITY_REQUESTED, { userId, callBack: resolveCallBack, showOffersUserDetailModal });
    });
  },
  fetchOffers() {
    Framework.getEventManager().publish(OFFERS_CONTENT_REQUESTED, {});
  },
  setOffersEligibility(data) {
    self.eligiblity = data;
    self.isEligiblityFetched = true;
  },
  setOffers(data) {
    self.offers_list = data;
    self.isOffersFetched = true;
  },
  setError() {
    self.isError = true;
  },
  setShowUserDetails(showUserDetails, reasonCode) {
    self.showUserDetails = showUserDetails;
    self.reasonCode = reasonCode;
  }
}));

export default Offersm;
